import {actionTypes} from '@servicenow/ui-core';
import {workAreaActions} from '../components/sn-em-metric-work-area/actions/workAreaActions';
import {createLog} from '../../utils/logger';
import {METRIC_CHART_OPERATION} from '../providers/sn-em-metric-explorer-provider/consts';
import {refreshActions} from './refreshActions';
import moment from 'moment';
import {calculateTimeStampUsingTimeZone, getStartAndEndTimesForPreset,
		convertTimeToRelativeServerTime, getOffsetBasedOnTimeAndZoneInMs} from '../../utils/dateUtils';

const LOG = createLog('metricActions');
const {COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

//TODO: If no usage please remove
const ACTION_TYPES = {
	METRIC_ADD_REQUESTED: 'METRIC_ADD#REQUESTED',
	METRIC_REMOVE_REQUESTED: 'METRIC_REMOVE#REQUESTED',
	METRICS_REMOVE_REQUESTED: 'METRICS_REMOVE#REQUESTED',
	TREE_SELECTION_CHANGED: 'TREE_SELECTION#CHANGED',
	...refreshActions.ACTION_TYPES,
	UPDATE_TIMEZONE_REQUESTED: 'UPDATE_TIMEZONE#REQUESTED'
};

const workAreaToggled = coeffects => {
	LOG.info('workAreaToggled');
	const {state, action, updateState} = coeffects;
	const {collapsed} = state;
	action.stopPropagation();
	updateState({collapsed: !collapsed});
};

const annotationsOnChartToggled = coeffects => {
	const {state, action: {payload, meta}, updateState} = coeffects;
	const {showAnnotationsOnChart} = state;
	const {value} = payload;
	const {actionType} = meta.appended;
	LOG.info('annotationsOnChartToggled', value, actionType);

	if (actionType === 'SHOW_ANNOTATIONS') {
		updateState({showAnnotationsOnChart: !showAnnotationsOnChart});
	}
};

const metricAddRequested = coeffects => {
	const {action, updateState} = coeffects;
	const {payload} = action;

	updateState({
		chartMetricsOperation: METRIC_CHART_OPERATION.METRIC_ADDED,
		metricMetadata: {...payload}
	});
};

const metricRemoveRequested = coeffects => {
	const {action, updateState} = coeffects;
	const {payload} = action;

	updateState({
		chartMetricsOperation: METRIC_CHART_OPERATION.METRIC_REMOVED,
		metricMetadata: {...payload}
	});
};

const metricsRemoveRequested = coeffects => {
	const {action, updateState} = coeffects;
	const {payload} = action;
	const {metrics} = payload;

	if (Array.isArray(metrics) && metrics.length > 0) {
		updateState({
			chartMetricsOperation: METRIC_CHART_OPERATION.METRICS_REMOVED,
			metricMetadata: metrics
		});
	}
};

const _getActualStartAndEndByPreset = params => {
	const {start: browserStartTime, end: browserEndTime, startTime, endTime, timePreset, zone} = params;
	let actualStartTime, actualEndTime, startTimeGMT, endTimeGMT;

	// we want to display start and end time relative to the
	// offset we got in timezone api call, we use a function to
	// convert the time with the offset from the server
	if (timePreset.toLowerCase() === 'custom') {
		// convert the time with the offset from the server
		const startTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, startTime);
		const endTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, endTime);
		actualStartTime = browserStartTime || convertTimeToRelativeServerTime(startTime, startTimeOffset);
		actualEndTime = browserEndTime || convertTimeToRelativeServerTime(endTime, endTimeOffset);
		startTimeGMT = calculateTimeStampUsingTimeZone(actualStartTime, startTimeOffset);
		endTimeGMT = calculateTimeStampUsingTimeZone(actualEndTime, endTimeOffset);
	} else {
		const startTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, browserStartTime);
		const endTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, browserEndTime);
		actualStartTime = browserStartTime ? convertTimeToRelativeServerTime(browserStartTime, startTimeOffset) : null;
		actualEndTime = browserEndTime ? convertTimeToRelativeServerTime(browserEndTime, endTimeOffset) : null;

		const result = getStartAndEndTimesForPreset(timePreset);
		startTimeGMT = result.start;
		endTimeGMT = result.end;
	}

	return {
		actualStartTime,
		actualEndTime,
		startTimeGMT,
		endTimeGMT
	};
};

const workAreaMetricDateChanged = coeffects => {
	LOG.info('workAreaMetricDateChanged');
	const {state, action, updateState} = coeffects;
	const {payload: {start, end, timePreset = 'custom'}} = action;
	const {timeConfig: {zone}, properties: {startTime, endTime}} = state;

	LOG.info('workAreaMetricDateChanged->newDate', {start, end});

	const {actualStartTime, actualEndTime, startTimeGMT, endTimeGMT} = _getActualStartAndEndByPreset({ start, end, startTime, endTime, timePreset, zone});

	action.stopPropagation();
	updateState({
		actualStartTime,
		actualEndTime,
		startTimeGMT,
		endTimeGMT,
		datePickerChanged: true,
		timePreset,
		// move toggle to off (performance-wise), if user wants more annotations he will click it again
		showAnnotationsOnChart: false
	});
};

const _startLastUpdatedTimer = coeffects => {
	LOG.log('_startLastUpdatedTimer fired');
	const {state: {refresh}, updateState, dispatch} = coeffects;
	const {lastTimeRefreshed} = refresh;

	const lastRefreshedTimeAgo = moment(lastTimeRefreshed).fromNow();

	const lastUpdatedInterval = setInterval(() => {
		dispatch([ACTION_TYPES.REFRESH_LAST_TIME_UPDATED]);
	}, 1000 * 60);

	updateState({
		refresh: {
			...refresh,
			lastUpdatedInterval,
			lastRefreshedTimeAgo
		}
	});
};

const init = coeffects => {
	let buildInfo = {};
	try {
		buildInfo = require('../../../buildInfo');
	} catch (e) {
		console.warn('*** Please run mvn install ***', e);  //eslint-disable-line no-console
	}

	console.info('sn-em-metric buildInfo', buildInfo); //eslint-disable-line no-console

	_startLastUpdatedTimer(coeffects);
};

const clearLastUpdatedInterval = coeffects => {
	LOG.log('clearLastUpdatedInterval fired');
	const {state: {refresh}} = coeffects;

	const {lastUpdatedInterval} = refresh;
	clearInterval(lastUpdatedInterval);
};

const updateTimeZoneConfig = coeffects => {
	const {updateState, action: {payload}} = coeffects;
	const {timeConfig} = payload;

	updateState({
		timeConfig
	});
};

const propertyHasChanged = coeffects => {
	const {action: {payload}, updateState} = coeffects;
	const {name, previousValue, value} = payload;

	switch(name){
		case 'chartIndex':
			LOG.info(`differentChartSelected': was ${previousValue} now ${value}`);
			// clear state in case of different chart selection
			updateState({
				actualStartTime: undefined,
				actualEndTime: undefined,
				showAnnotationsOnChart: false,
				datePickerChanged: false
			});
			break;
	}
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[COMPONENT_PROPERTY_CHANGED]: propertyHasChanged,
	[COMPONENT_DISCONNECTED]: clearLastUpdatedInterval,
	[workAreaActions.ACTION_TYPES.WORK_AREA_TOGGLED]: workAreaToggled,
	[workAreaActions.ACTION_TYPES.WORK_AREA_METRIC_DATE_CHANGED]: workAreaMetricDateChanged,
	[ACTION_TYPES.METRIC_ADD_REQUESTED]: metricAddRequested,
	[ACTION_TYPES.METRIC_REMOVE_REQUESTED]: metricRemoveRequested,
	[ACTION_TYPES.METRICS_REMOVE_REQUESTED]: metricsRemoveRequested,
	'NOW_TOGGLE#CHECKED_SET': annotationsOnChartToggled,
	[ACTION_TYPES.UPDATE_TIMEZONE_REQUESTED]: updateTimeZoneConfig
};

export const metricActions = {
	ACTION_TYPES,
	actionHandlers
};
