import moment from 'moment';
import '@servicenow/now-button';
import {createLog} from '../../../../../utils/logger';
import '../../../../providers/sn-em-metric-explorer-provider';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-chart-timeseries';
import '@servicenow/now-chart-navigator';
import {getPropertiesListFromProvider} from '../../../../../utils/providerUtils';
import {getOffsetBasedOnTimeAndZoneInMs} from '../../../../../utils/dateUtils';

const LOG = createLog('snEmMetricChartView');

const getProviderPropertiesForJSX = (state) => {
	const {properties: {providerId}} = state;

	const propertiesToMapToProvider = [
		'chartConfig.accessibilityConfig',
		'chartConfig.extents',
		'chartConfig.extraMargin',
		'chartConfig.registerConfig',
		'chartConfig.seriesConfig',
		'chartConfig.annotationsData',
		'chartConfig.annotationsConfig',
		'chartConfig.yAxisConfig',
		'chartConfig.seriesConfig',
		'chartConfig.confidenceBandConfig',
		'chartConfig.confidenceBandData',
		'chartConfig.xFormat'
	];

	return getPropertiesListFromProvider(providerId, propertiesToMapToProvider);
};

const _generateKey = seriesData=> {
	return Array.isArray(seriesData) && seriesData.length > 1
		? `${seriesData[0].timestamp}-${seriesData[seriesData.length - 1].timestamp}`
		: 'key';
};

const getNowChartTimeseriesJSX = (state, providerProperties) => {
	const {chartOptions, properties: {seriesData, confidenceBandConfig}} = state;
	const {
		accessibilityConfig,
		extents,
		extraMargin,
		registerConfig,
		seriesConfig,
		annotationsData,
		annotationsConfig,
		yAxisConfig,
		confidenceBandData,
		xFormat
	} = providerProperties;

	const key = confidenceBandConfig ? 'chart-key-with-band-config' : 'chart-key';

	return (
		<now-chart-timeseries
			key={key}
			accessibility-config={accessibilityConfig}
			extents={extents}
			extra-margin={extraMargin}
			register-config={registerConfig}
			series-config={seriesConfig}
			series-data={seriesData}
			confidence-band-data={confidenceBandData}
			confidence-band-config={confidenceBandConfig}
			annotations-data={annotationsData}
			annotations-config={annotationsConfig}
			y-axis-config={yAxisConfig}
			x-format={xFormat}
			x-axis-type="time"
			register-position="top"
			width="auto"
			height="auto"
			show-tooltip
			{...chartOptions}
		/>
	);
};

const getNowChartNavigatorJSX = (state, providerProperties) => {
	const {seriesConfig} = providerProperties;
	const {properties: {startTime, endTime, seriesData, datePickerChanged, timeConfig}} = state;
	const {zone} = timeConfig;
	let seriesDataParam, seriesConfigParam;

	//When the series data is empty show the navigator with the dates of the date picker
	if (Array.isArray(seriesData) && seriesData.length === 0) {
		seriesDataParam = [{'timestamp': startTime}, {'timestamp': endTime}];
		seriesConfigParam = {'start': {'x': 'timestamp'}, 'end': {'x': 'timestamp'}};
	} else {
		seriesDataParam = [...seriesData];
		seriesConfigParam = seriesConfig;
	}

	const calculateLocalTime = (time) => {
		const localOffsetInMin = moment(time).utcOffset();

		if (datePickerChanged) {
			return time + localOffsetInMin * 60 * 1000;
		} else {
			// while date picker wasn't changed the provided time is UTC with GMT offset
			// need to fix it in order to get into metrics data range
			const tzOffset = getOffsetBasedOnTimeAndZoneInMs(zone, time);
			return time + tzOffset;
		}
	};

	// we must stretch chart/chart-navigator to the full scale that was set by date range picker.
	// since series data is converted to local time zone we must also modify the utc start time/end time
	// to local timezone
	const extents = {
		x: [calculateLocalTime(startTime),
			calculateLocalTime(endTime)]
	};

	const key=_generateKey(seriesData);

	return (
		<now-chart-navigator
			key={key}
			extents={extents}
			extra-margin={{'top':24,'bottom':16}}
			height="81"
			width="auto"
			series-config={seriesConfigParam}
			series-data={seriesDataParam}
			x-axis-type="time"
			y-axis-type="linear"
			range-label-config={{'format':'%d/%m/%Y %H:%M'}}
		/>
	);
};

export const view = state => {
	LOG.info('Rendering');
	const providerProperties = getProviderPropertiesForJSX(state);

	return (
		<Fragment>
			{getNowChartTimeseriesJSX(state, providerProperties)}
			{getNowChartNavigatorJSX(state, providerProperties)}
		</Fragment>
	);
};

