export const getUniqueProviderId = (type, name, ciSysId, chartIndex) => {
	//Unique among workspace TABS
	if (type && type !== '') {
		return `${type}_${name}_${ciSysId}_${chartIndex}`;
	} else {
		return `${name}_${ciSysId}_${chartIndex}`;
	}
};

export const getPropertyFromProvider = (providerId, propertyPath) => {
	return `@${providerId}/${propertyPath}`;
};

const _getPropertyNameFromFullPath = propertyPath => {
	return propertyPath.substring(propertyPath.lastIndexOf('.') + 1);
};

export const getPropertiesListFromProvider = (providerId, properties) => {
	const keys = properties.map(item => {
		if (item.indexOf('.') === -1) {
			return {
				key: item,
				value: item
			};
		} else {
			return {
				key: _getPropertyNameFromFullPath(item),
				value: item
			};
		}
	});

	return keys.reduce((acc, currentValue) => {
		const {key, value} = currentValue;
		acc[key] = getPropertyFromProvider(providerId, value);

		return acc;
	}, {});
};
