import '@servicenow/now-button';
import {t} from 'sn-translate';
import {createLog} from '../../../utils/logger';

const LOG = createLog('snEmMetricHeaderView');
const I18N = {
	SAVE_TO_ALERT_TXT: t('Save to alert'),
	COPY_LINK_TXT: t('Copy link'),
	REFRESH_LABEL: t('Refresh chart')
};

export const view = state => {
	const {properties: {ciName, lastRefreshedTimeAgo}} = state;
	LOG.info('Rendering ciName-', ciName);

	const title = t('{ciName} metrics', {ciName});
	const lastRefreshedMessage = t('Last refreshed {lastRefreshedTimeAgo}', {lastRefreshedTimeAgo});

	return (
		<div>
			<header>
				<now-heading label={title} variant="header-primary"/>
				<section>
					<now-button label={I18N.SAVE_TO_ALERT_TXT} variant="secondary" size="md"/>
					<now-button label={I18N.COPY_LINK_TXT} variant="secondary" size="md"/>
					<now-button-iconic
						icon="sync-fill"
						variant="secondary"
						size="md"
						tooltipContent={I18N.REFRESH_LABEL}
					/>
				</section>
			</header>
			<div className="last-refresh-time">
				{lastRefreshedMessage}
			</div>
		</div>

	);
};
