import {createCustomElement} from '@servicenow/ui-core';
import {view} from './snEmMetricWorkAreaView';
import styles from './snEmMetricWorkArea.scss';
import {workAreaActions} from './actions/workAreaActions.js';
import {t} from 'sn-translate';

createCustomElement('sn-em-metric-work-area', {
	view,
	styles,
	initialState: {
		showAnnotationsOnChart: false,
		preset: undefined,
		headerRef: undefined
	},
	properties: {
		collapsed: {
			default: true,
			required: true,
			schema: {type: 'boolean'}
		},
		ciSysId: {},
		startTime: {},
		endTime: {},
		empty: {
			default: true,
			schema: {type: 'boolean'}
		},
		hasError: {
			default: false,
			schema: {type: 'boolean'}
		},
		chartMetadata: {},
		dataAvailable: {
			schema: {type: 'boolean'}
		},
		chartTitle: {
			default: t('Custom chart')
		},
		providerId: {},
		timeConfig: {},
		datePickerChanged: false,
		annotationsToggled: false,
		chartIndex: -2
	},
	actions: {
		...workAreaActions.action
	},
	actionHandlers: {
		...workAreaActions.actionHandlers
	},
	dispatches: {
		/**
		 * Bubble up the event when the Expand/ Collapse icon is clicked
		 */
		WORK_AREA_TOGGLED: 'WORK_AREA#TOGGLED'
	}
});
