import {t} from 'sn-translate';

const TYPEAHEAD_ITEMS = {
	TYPES: {METRIC: 0, CI: 1, CLASS: 2},
	sections: [{
		'label': t('Metrics'),
		'children': []
	}, {
		'label': t('Configuration items'),
		'children': []
	}, {
		'label': t('CI class'),
		'children': []
	}],
	getItems: () => {
		return TYPEAHEAD_ITEMS.sections;
	},
	addItems: (items, type) => {
		TYPEAHEAD_ITEMS.sections[type].children = items.map(label => {
			return {'id': label, label};
		});
	},
	sort: (...types) => {
		// sort by name
		const _sort = (nameA, nameB) => {
			if (nameA.label < nameB.label) {
				return -1;
			}
			if (nameA.label > nameB.label) {
				return 1;
			}
			return 0; // names must be equal
		};
		const _sections = types || Object.values(TYPEAHEAD_ITEMS.TYPES);
		_sections.forEach(type => TYPEAHEAD_ITEMS.sections[type].children.sort(_sort));
	}
};

const getItems = entities => {
	TYPEAHEAD_ITEMS.addItems(Array.from(entities.metrics), TYPEAHEAD_ITEMS.TYPES.METRIC);
	TYPEAHEAD_ITEMS.addItems(Array.from(entities.ci), TYPEAHEAD_ITEMS.TYPES.CI);
	TYPEAHEAD_ITEMS.addItems(Array.from(entities.class), TYPEAHEAD_ITEMS.TYPES.CLASS);
	TYPEAHEAD_ITEMS.sort();
	return TYPEAHEAD_ITEMS.getItems();
};

export const transformToTypeahead = {
	getItems
};
