import moment from 'moment';
import {isEmpty as _isEmpty, uniq as _uniq, isUndefined as _isUndefined} from 'lodash';
import {METRIC_CHART, RANGE_PICKER, CHART, CHART_SERIES, SERIES_TYPE} from '../consts';
import {getOffsetBasedOnTimeAndZoneInMs} from '../../../../utils/dateUtils';

const MAX_NUMBER_OF_RESOURCES = 10;

/**
 *  Build bound configuration especially when data time picker been changed
 * @param ciSysId
 * @param metrics
 * @param timeConfig
 * @param numOfChartsPerWidgets
 * @returns {{ci: *, start: *, end: *, metrics: *}}
 */
export const buildBoundsConfiguration = (ciSysId, metrics, timeConfig, numOfChartsPerWidgets) => {
	const {startTime, endTime, zone} = timeConfig;

	const offset = getOffsetBasedOnTimeAndZoneInMs(zone, startTime);
	const boundConfigurations = metrics.map(metric => {
		const metricTypeId = metric.metric_type_id;
		const resourcesId = Object.keys(metric.resources_id);

		if (!resourcesId.length) {
			return [
				{
					type: metricTypeId,
					series: METRIC_CHART.SERIES,
					resource: ''
				}
			];
		}

		return resourcesId.map(resourceId => {
			return {
				type: metricTypeId,
				series: METRIC_CHART.SERIES,
				resource: resourceId
			};
		});
	});

	return {
		ci: ciSysId,
		start: startTime,
		end: endTime,
		tzOffset: offset,
		zone,
		metrics: boundConfigurations.slice(0, numOfChartsPerWidgets)
	};
};

/**
 *
 * @param metadata
 * @returns {*[]}
 */
export const buildDataConfigurations = (metadata) => {
	const {title, chart_type: chartType, metrics} = metadata;

	const metricsTinyNames = metrics.reduce((result, metric) => {
		result[metric['metric_type_id']] = {
			name: metric['metric_tiny_name'],
			resourcesId: metric.resources_id
		};
		return result;
	}, {});

	return {
		title,
		chartType,
		metricsTinyNames
	};
};

export const getConfigurationData = (state, metricsData, chartMetadata) => {
	const {properties} = state;

	const {
		ciSysId,
		showBounds,
		startTime,
		endTime,
		timeConfig: timeConfigParams
	} = properties;


	const {zone} = timeConfigParams;
	const {charts_per_widget: numOfChartsPerWidgets = 10, metrics: metricsMetadata} = chartMetadata;

	const timeConfig = {
		startTime,
		endTime,
		offset: getOffsetBasedOnTimeAndZoneInMs(zone, startTime),
		zone,
	};

	const dataConfigurations = buildDataConfigurations(chartMetadata);
	const boundsConfiguration = buildBoundsConfiguration(ciSysId, metricsMetadata, timeConfig, numOfChartsPerWidgets);

	return {
		dataConfigurations,
		boundsConfiguration,
		showBounds: !!showBounds,
		metricData: metricsData,
		metricsMetadata
	};
};

export const getUnit = metricData => {
	const units = metricData.map(bound => {
		return bound.unit;
	});

	// Case when there one or metrics without units
	if (units.length !== metricData.length)
		return '';

	const unit = units[0];
	return _uniq(units).length === 1 ? (unit !== '' ? unit : 'numeric') : '';
};

export const getUnits = metricData => {
	const units = metricData.map(item => item.unit);

	const uniqueUnits = [...new Set(units)];

	if (uniqueUnits.length === 1) {
		const unit = units[0];
		return unit !== [''] ? [unit] : ['numeric'];
	} else {
		return uniqueUnits.map(unit => unit === '' ? 'numeric' : unit);
	}
};

const _buildBounds = series => {
	const lowerBounds = [...series.filter(item => item.type === SERIES_TYPE.LOWER)];
	const upperBounds = [...series.filter(item => item.type === SERIES_TYPE.UPPER)];

	return {
		lower: [...lowerBounds],
		upper: [...upperBounds]
	};
};

export const buildMetricsData = (params) => {
	const {
		metrics,
		dataConfigurations: {
			metricsTinyNames
		},
	} = params;

	if (_isUndefined(metrics) || _isEmpty(metrics)) {
		return [];
	}

	return metrics.map(series => {
		const metricObj = metricsTinyNames[series.type];
		const {unit} = series;

		if (metricObj) {
			const {name} = metricObj;

			const bounds = _buildBounds(series.series);

			return {
				...series,
				chartType: CHART.CHART_TYPES.LINE_SERIES,
				name,
				unit: unit === '' ? 'numeric' : unit,
				bounds
			};
		} else {
			return {};
		}
	});
};

// Function to aid in reading undefined HTML attributes during initialization. The issue is, if you define any
// attributes, only those attributes will be read into properties. If you define no attributes, then the default
// values will be read. We need a mix of both.
export const readAttributes = (host, ...attributes) => {
	let payload = {};
	attributes.forEach((attr) => {
		let initial = host.getAttribute(attr);
		if (initial)
			payload[attr] = initial;
	});
	return payload;
};

// Returns the platform's datetime format for moment
export const getDatetimeFormat = (dateStringFormat, timeStringFormat) => {
	if (dateStringFormat && timeStringFormat) {
		dateStringFormat = dateStringFormat
			.replace(/yyyy/, 'YYYY')
			.replace(/mm/, 'MM')
			.replace(/dd/, 'DD');
		return `${dateStringFormat} ${timeStringFormat}`;
	}

	return RANGE_PICKER.DEFAULT_DATE_FORMAT;
};

// Converts absolute epoch time in milliseconds to a moment object at local time
export const convertEpochToLocal = (time, offset) => {
	// If offset is not passed, use the Date API to get browser offset
	if (offset === null || offset === undefined) {
		// Date API returns the difference in minutes, so multiplying by -1 is needed
		offset = new Date().getTimezoneOffset() * -1;
	} else {
		// Platform gives utc offset in milliseconds and moment expects it in minutes
		offset = offset / 1000 / 60;
	}

	return moment.utc(time).utcOffset(offset);
};

export const generateMetricsForPayload = metrics => {
	const modifiedMetrics = [];

	metrics.forEach(metric => {
		const {metric_type_id: type, resources_id} = metric;
		const resourcesIds = Object.keys(resources_id);
		// get top 10 resources ids
		const keys = resourcesIds.slice(0, MAX_NUMBER_OF_RESOURCES);

		if (!keys.length) {
			modifiedMetrics.push({
				type,
				series: CHART_SERIES,
				resource: ''
			})
		} else {
			keys.forEach(key => {
				modifiedMetrics.push({
					type,
					series: CHART_SERIES,
					resource: key
				});
			});
		}
	});

	return modifiedMetrics;
};

export const createPayloadForChartFetch = params => {
	const {ciSysId, startTime, endTime, metrics, type} = params;

	const formattedMetricsForAPI = generateMetricsForPayload(metrics);

	return {
		params: {
			ci: ciSysId,
			start: startTime,
			end: endTime,
			metrics: formattedMetricsForAPI,
			type
		}
	};
};

export const sortMetricsByRelatedCI = (metrics = []) => {
	const mapping = new Map();
	metrics.forEach(metric => {
		const {ci_sys_id: ciSysId} = metric;
		const metricArr = mapping.has(ciSysId)? mapping.get(ciSysId) : [];
		metricArr.push(metric);
		mapping.set(ciSysId, metricArr);
	});
	return mapping;
};

/**
 * create an empty metric data if no data was fetched from server just for the sake of legend to exist
 * current behaviour of now-chart-timeseries is not to display legend if series data doesn't exist
 * @param startTime
 * @param endTime
 * @param metrics
 * @returns {[]}
 */
export const normalizeMetricData = (startTime, endTime, metrics, ciSysId) => {
	const result = [];
	if (Array.isArray(metrics) && metrics.length > 0){
		metrics.forEach(metric => {
			if (Array.isArray(metric.series) && metric.series.length > 0) {

				// find out if data exist at all
				const isNotEmpty = metric.series.reduce(
					(isWithData, series) => !!(isWithData || series.data.length) , false)

				metric.ci = ciSysId;

				if (isNotEmpty){
					result.push(metric);
				} else {
					// generate empty series data
					const newMetric = {
						...metric,
						series: [
							{
								type: "RAW",
								data: [ {x: startTime}, {x: endTime}]
							}
						]
					};
					result.push(newMetric);
				}
			}
		})
	}

	return result;
}
