import {isEmpty as _isEmpty, sortBy as _sortBy} from 'lodash';
import {GRID} from '../consts';
import momentTz from 'moment-timezone';
import {t} from 'sn-translate';
import moment from 'moment';

const TIME = {
	MAX_MIN_TO_SUPPORT_SEC: 15,
	SEC: 1000,
	HOUR: 1000 * 60 * 60,
	NUM_OF_SECS_IN_MIN: 60,
	NUM_HOURS_IN_DAY: 24,
	NUM_DAYS_IN_MONTH: 32,
	NUM_DAYS_IN_WEEK: 7,
	NUM_OF_MONTHS_IN_YEAR: 12
};

const STATIC_SERIES_DATA = {
	height: 'auto',
	width: 'auto',
	showTooltip: true,
	extraMargin: {top: 10, bottom: 5, right: 10, left: 10},
	registerConfig: {
		legendOnly: false,
		justifyItems: 'start',
		expandToFit: true,
		disableGrid: true
	},
	accessibilityMode: false,
	accessibilityConfig: {
		dashPatterns: false,
		monochrome: false,
		strokeWidth: false,
		opacity: true
	}
};

const CONFIDENCE_BAND_LABEL = t('Confidence Band');

const getLabelTruncation = chartsNum => {
	switch (chartsNum) {
	case 1:
		return 55;
	case 2:
		return 27;
	case 3:
		return 16;
	case 4:
		return 13;
	default:
		return 10;
	}
};

const _getYAxis = (units, unit) => {
	if (units.length === 1) {
		return 'primary';
	} else if (units.length === 2) {
		return units.findIndex(item => item === unit) === 0 ? 'primary' : 'secondary';
	} else {
		return null;
	}
};

const _getLabel = ({name, resourceName, seriesUnit, ciNameToDisplay}) => {
	return `${name}${resourceName ? '/' + resourceName:  ''} ${seriesUnit}, ${ciNameToDisplay}`;
}

const buildUniqueMetricKey = (name, ci, metricType, resourceId= '0') => {
	return `${name}_${ci}_${metricType}_${resourceId}`;
}
const buildSeriesConfig = (dataSet, ciName, units, chartMetadata) => {
	const labelTruncation = getLabelTruncation(dataSet.length);

	//TODO: move to outside const
	const MAX_RESULTS = 10;

	//limit dataSet to MAX_RESULTS
	const truncatedDataset = dataSet.slice(0, MAX_RESULTS);

	return truncatedDataset.map((seriesData, idx) => {
		const {name, chartType} = seriesData;
		const modifiedChartType = chartType === GRID.CHART_TYPES.STACKED_BAR_SERIES ? 'bar' : chartType.toLowerCase();
		const showGaps = chartType === GRID.CHART_TYPES.STACKED_BAR_SERIES;

		const seriesObj = {};

		const axis = _getYAxis(units, seriesData.unit);
		const seriesUnit = ` (${_getYAxisTitle(seriesData.unit)})`
		const metricMetaData = chartMetadata.metrics.find(metric => {
			return metric.metric_type_id === seriesData.type && metric.ci_sys_id === seriesData.ci;
		});
		let ciNameToDisplay;
		if (metricMetaData && metricMetaData.ci_name) {
			ciNameToDisplay = metricMetaData.ci_name;
		} else {
			ciNameToDisplay = ciName;
		}

		let label;
		const resourcesIdEntries = Object.entries(metricMetaData.resources_id);

		if (resourcesIdEntries.length === 1) {
			const [resourceId, resourceName] = resourcesIdEntries[0] || [];
			label = _getLabel({name, resourceName, seriesUnit, ciNameToDisplay});
			const objectKey = buildUniqueMetricKey(name, seriesData.ci, seriesData.type, resourceId);

			seriesObj[objectKey] = {
				x: 'timestamp',
				y: 'y' + idx,
				showGaps: showGaps,
				type: [modifiedChartType],
				label,
				strokeWidth: 2,
				labelTruncation
			};

			if (axis) {
				seriesObj[objectKey].axis = axis;
			}
		} else {
			const resource = resourcesIdEntries.find(([resourceKey]) => {
				return resourceKey === seriesData.resource;
			});

			const [resourceId = '0', resourceName] = resource || [];
			const seriesObjKey = buildUniqueMetricKey(name, seriesData.ci, seriesData.type, resourceId);
			label = _getLabel({name, resourceName, seriesUnit, ciNameToDisplay});

			seriesObj[seriesObjKey] = {
				x: 'timestamp',
				y: 'y' + idx,
				showGaps: showGaps,
				type: [modifiedChartType],
				label,
				strokeWidth: 2,
				labelTruncation
			};

			if (axis) {
				seriesObj[seriesObjKey].axis = axis;
			}
		}

		return seriesObj;
	}).reduce((result, object) => {
		const key = Object.keys(object)[0];
		result[key] = object[key];
		return result;
	}, {});
};

const buildTimeStampAndYAxisForData = (data, index, offset) => {
	return data.map(chartInput => {
		const {x, y} = chartInput;

		const timeStampConvertedToZone = x + offset;

		return {
			timestamp: timeStampConvertedToZone,
			[`y${index}`]: y
		};
	});
};

const convertTimeSeriesArrayIntoObject = (data, index, allSeries) => {
	return data.reduce((result, object) => {
		const yParam = `y${index}`;

		// Case we had already this timeStamp, so we should update the original timestamp
		if (allSeries[object.timestamp]) {
			allSeries[object.timestamp][yParam] = object[yParam];
		} else { // It's a new timestamp, we add that to the result
			result[object.timestamp] = object;
		}

		return result;
	}, {});
};

const buildSeriesData = (dataSet, offsetMsInUserTimezone) => {
	let allSeries = {};

	dataSet.forEach((seriesData, index) => {
		const seriesWithData = seriesData.series.filter(item => item.data.length > 0);
		const data = seriesWithData.length === 0 ? [] : [...seriesWithData[0].data];

		let timeSeriesArray, timeSeries;
		if (data.length === 0) {
			timeSeries = {};
		} else {
			timeSeriesArray = buildTimeStampAndYAxisForData(data, index, offsetMsInUserTimezone);
			timeSeries = convertTimeSeriesArrayIntoObject(timeSeriesArray, index, allSeries);
		}

		if (_isEmpty(Object.keys(allSeries))) {
			Object.assign(allSeries, timeSeries);
		} else {
			allSeries = {
				...timeSeries,
				...allSeries
			};
		}
	});

	return Object.values(allSeries);
};

const buildConfidenceBand = (dataSet, offsetMsInUserTimezone) => {
	if (!_isEmpty(dataSet) && dataSet.length === 1 && dataSet[0].chartType === GRID.CHART_TYPES.LINE_SERIES &&
		!_isEmpty(dataSet[0].bounds)) {
		const {bounds} = dataSet[0];

		const formattedBounds = [
			{series: [...bounds.lower]},
			{series: [...bounds.upper]}
		];

		const confidenceBandData = buildSeriesData(formattedBounds, offsetMsInUserTimezone);
		const confidenceBandConfig = {
			'config': {x: 'timestamp', y0: 'y0', y1: 'y1', label: CONFIDENCE_BAND_LABEL}
		};

		return {
			confidenceBandData,
			confidenceBandConfig
		};
	} else {
		return {};
	}

};
/**
 * Update xAxis range accroding to start and end time. Update Y only to be absolute when PERCENTAGE %
 * @param dataSet
 * @param start
 * @param end
 */
const updateExtents = (dataSet, start, end) => {
	if (!_isEmpty(dataSet) && dataSet[0].unit === '%')
		STATIC_SERIES_DATA['extents'] = {x: [start, end], y: [0, 100]};
	else
		STATIC_SERIES_DATA['extents'] = {x: [start, end]};
};

/**
 *  Update xFormat so the time will appear correctly by considering also time zone
 * @param start
 * @param end
 * @param zone
 */
const updateXformat = (start, end, zone) => {
	const intervalDifference = end - start;
	let format = 'HH:mm';

	const startDay = momentTz(start).tz(zone).isoWeekday();
	const endDay = momentTz(end).tz(zone).isoWeekday();

	if ((intervalDifference / TIME.SEC) < (TIME.MAX_MIN_TO_SUPPORT_SEC * TIME.NUM_OF_SECS_IN_MIN))
		format = 'HH:mm:ss';
	else if ((intervalDifference / TIME.HOUR) < TIME.NUM_HOURS_IN_DAY) {
		if (startDay !== endDay) {
			format = 'MMM D, HH:mm';
		} else {
			format = 'HH:mm';
		}
	} else if ((intervalDifference / (TIME.HOUR * TIME.NUM_HOURS_IN_DAY)) < 3) {
		format = 'MMM D, HH:mm';
	}
	//Week (Comparing Days)
	else if ((intervalDifference / (TIME.HOUR * TIME.NUM_HOURS_IN_DAY)) < TIME.NUM_DAYS_IN_WEEK) {
		format = 'MMM D, YYYY';
	}
	//Month (Comparing Days)
	else if ((intervalDifference / (TIME.HOUR * TIME.NUM_HOURS_IN_DAY))
		< TIME.NUM_DAYS_IN_MONTH) {
		format = 'MMM D, YYYY';
	}
	//Year (Comparing Months)
	else if ((intervalDifference / (TIME.HOUR * TIME.NUM_HOURS_IN_DAY * TIME.NUM_DAYS_IN_MONTH))
		< TIME.NUM_OF_MONTHS_IN_YEAR) {
		// show date as well, other wise we will not be able to tell the difference on the chart in such timeline
		format = 'MMM D, YYYY';
	} else { // in case for more than 1 year.
		format = 'MMM YYYY';
	}

	// we use utc in moment to align with the data that we get back from the server
	// and to prevent setting "double offsets"
	STATIC_SERIES_DATA.xFormat = (ts) => moment(ts).utc().format(format);
};

const _getYAxisTitle = title => {
	switch(title) {
		case undefined:
		case '':
		case '#':
			return 'numeric';
		default:
			return title;
	}
};

const _getYAxisConfig = (units, primaryTitle, secondaryTitle) => {
	const primaryAxisTitle = _getYAxisTitle(primaryTitle);

	if (units.length === 1) {
		return {
			title: primaryAxisTitle
		};
	} else if (units.length === 2) {
		const secondaryAxisTitle = _getYAxisTitle(secondaryTitle);

		return {
			primary: {
				title: primaryAxisTitle,
				order: 1,
				orientation: 'left',
			},
			secondary: {
				title: secondaryAxisTitle,
				order: 2,
				orientation: 'right'
			}
		};
	} else {
		return {
			tickConfig: {disabled: true},
			strokeWidth: 0
		};
	}
};

export const getChartConfig = (chartData, start, end, zone, ciName, units, datePickerChanged, chartMetadata, offsetMsInUserTimezone) => {
	const seriesConfig = buildSeriesConfig(chartData, ciName, units, chartMetadata);
	const [primaryTitle, secondaryTitle] = units;

	const yAxisConfig = _getYAxisConfig(units, primaryTitle, secondaryTitle);
	const seriesData = buildSeriesData(chartData, offsetMsInUserTimezone);

	const confidenceConfigurations = buildConfidenceBand(chartData, offsetMsInUserTimezone);
	updateExtents(chartData, start, end);
	updateXformat(start, end, zone, datePickerChanged);

	const sortedSeriesData = _sortBy(seriesData, 'timestamp');

	return {
		...STATIC_SERIES_DATA,
		seriesConfig,
		seriesData: sortedSeriesData,
		...confidenceConfigurations,
		yAxisConfig
	};
};
