import {createCustomElement} from '@servicenow/ui-core';

import {view} from './snEmMetricNavigatorPanelView';
import {transformState} from './snEmMetricNavigatorPanelTransform';
import styles from './snEmMetricNavigatorPanel.scss';
import {navigationPanelActions} from './actions/navigationPanelActions';

const holdsTreeTyping = {
	count: 0,
	items: [],
	mapping: new Map(),
	selectedItems: [],
	expandedItems: []
};

createCustomElement('sn-em-metric-navigator-panel-popover-content', {
	view: (state) => <div style={{width: '15em', padding: '1em'}}>{state.properties.content || <slot />}</div>,
	properties: {
		content: {}
	}
});

createCustomElement('sn-em-metric-navigator-panel', {
	view,
	transformState,
	styles,
	initialState: {
		treeSelectionChanged: {},
		isMaxLimitReached: false,
		isShowNotification: false,
		search: {
			searchItems: [],
			searchFilter: ''
		},
		treeExplorer: {
			ci: {
				...holdsTreeTyping
			},
			host: {
				...holdsTreeTyping
			},
			service: {
				...holdsTreeTyping
			}
		}
	},
	properties: {
		collapsed: false,
		chartMetadata: {},
		metricsCiData: {},
		metricsHostData: {},
		metricsServiceData: {},
		metricsSelected: [],
		chartIndex: -2
	},
	actionHandlers: {
		...navigationPanelActions.actionHandlers
	}
});

