/*
 * Copied from sn-component-form. Many component are using same way as implemented below with minor tweaks.
 */
const BUSSINESS_UNIT = 'EM';
const UTIL = 'LOGGER';
const LOG_LEVEL = '__SEISMIC_LOG_LEVEL';

export const LOG_LEVEL_DEBUG = 1;
export const LOG_LEVEL_INFO = 2;
export const LOG_LEVEL_LOG = 3;
export const LOG_LEVEL_WARN = 4;
export const LOG_LEVEL_ERROR = 5;

const packageJson = require('../../package');
export const MODULE = packageJson.name;

if (!window[BUSSINESS_UNIT])
	window[BUSSINESS_UNIT] = {};

if (!window[BUSSINESS_UNIT][UTIL])
	window[BUSSINESS_UNIT][UTIL] = {
		[MODULE]: {
			setLevel: (className, newLevel) => window[BUSSINESS_UNIT][UTIL][MODULE][className][LOG_LEVEL] = newLevel,
		}
	};

/**
 * Simple logging utility to include context
 * @usage const LOG = createLog('ComponentName');
 *
 * @param name
 * @return {{log, debug, warn, error}}
 */
export function createLog(name) {
	const _log = (type, level, args) => {
		if (level < window[BUSSINESS_UNIT][UTIL][MODULE][name][LOG_LEVEL] || args < 1) {
			return;
		}
		args[0] = `(${MODULE}->${name}) ${args[0]}`;
		console[type].apply(console, args); // eslint-disable-line no-console
	};

	// Register Class & set default logging level
	window[BUSSINESS_UNIT][UTIL][MODULE][name] = {[LOG_LEVEL]: LOG_LEVEL_ERROR};

	return {
		debug: (...args) => _log('debug', LOG_LEVEL_DEBUG, args),
		log: (...args) => _log('log', LOG_LEVEL_LOG, args),
		info: (...args) => _log('info', LOG_LEVEL_INFO, args),
		warn: (...args) => _log('warn', LOG_LEVEL_WARN, args),
		error: (...args) => _log('error', LOG_LEVEL_ERROR, args)
	};
}
