import {isEmptyResources} from '../../../../utils/objUtils';
import {treeConstants} from '../../../constants/tree';

const buildMetricsTree = (node, ciSysId, ciName) => {
	const result = [];
	const mapping = new Map();

	const commonPayload = item => ({
		metricTypeId: item.metric_type_id,
		metricTypeName: item.metric_type,
		metricTypeTinyName: item.metric_type_tiny_name,
		identifierProps: {type: 'icon', icon: 'chart-line-outline'},
		ciSysId,
		ciName
	});

	node.forEach( item => {
		// if resource ids exist do not put the entire metric only the resources
		if (item.resources_id && !isEmptyResources(item.resources_id)){
			for (let resource in item.resources_id) {
				const metric  = {
					id: `${treeConstants.LEAF}-${item.metric_type_id}-${resource}`,
					label: `${item.metric_type} / ${item.resources_id[resource]}`,
					resourceId: resource,
					resourceName: item.resources_id[resource],
					...commonPayload(item)
				};
				result.push(metric);
				mapping.set(treeConstants.generateMapKey(ciSysId, metric.id), metric);
			}
		} else {
			const metric = {
				id: `${treeConstants.LEAF}-${item.metric_type_id}`,
				label: item.metric_type,
				...commonPayload(item)
			};
			result.push(metric);
			mapping.set(treeConstants.generateMapKey(ciSysId, metric.id), metric);
		}
	});
	return {
		metrics: result,
		mapping
	};
};


//TODO: verify that I really need the function
const getAllMetricsInTreeAsItems = (tree, prefix = [], items = [], metrics = []) => {
	if (tree) {
		const node = tree[0] ? tree[0] : tree;
		if (node.id) {
			if (node.id.startsWith(treeConstants.LEAF)){
				if (metrics.includes(node.id)) {
					items.push([...prefix, node.id]);
				}
			}

			const newPrefix = [...prefix, node.id];
			if (node.children && node.children.length > 0) {
				node.children.forEach(child => getAllMetricsInTreeAsItems(child, newPrefix, newPrefix, metrics));
			}
		}
	}
};

// create nodes in list that need to be declared in order for tree to expand
// the relevant parts
const breakItemToParts = (prefix, items = new Set(), item) => {
	if (item.length > 0) {
		const part = item.shift();
		const itemToPush = [...prefix, part];
		items.add(itemToPush);
		breakItemToParts(itemToPush, items, item);
	}
};

export {
	buildMetricsTree,
	getAllMetricsInTreeAsItems,
	breakItemToParts
};
