import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import cuid from 'cuid';

const NOT_FOUND = '@@-NOT_FOUND-@@';
const _isObject = T => ({}).toString.apply(T) === '[object Object]';
const _isArray = T => ({}).toString.apply(T) === '[object Array]';

const _toObject = (GQLObj) => {
	const result = {};
	for (let i in GQLObj) {
		if (_isObject(GQLObj[i]) && GQLObj[i].hasOwnProperty('displayValue')) {
			result[i] = GQLObj[i].displayValue || '';
		} else if (_isObject(GQLObj[i]) && GQLObj[i].hasOwnProperty('value')) {
			result[i] = GQLObj[i].value || '';
		} else if (_isObject(GQLObj[i]) && GQLObj[i].hasOwnProperty('_reference')) {
			for (let key in GQLObj[i]._reference){
				const newKey = (key) ? `${i}.${key}` : i;
				result[newKey] = (key) ? _toObject(GQLObj[i]._reference)[key] : '';
			}
		} else {
			result[i] = GQLObj[i] || '';
		}
	}
	return result;
};

const _toArray = (GQLArray) => {
	return GQLArray.reduce((result, item) => {
		const obj = _toObject(item);
		result.push(obj);
		return result;
	}, []);
};

const _dfs = (obj, searchTerm) => {
	if (!_isObject(obj))
		return NOT_FOUND;

	for (let key in obj) {
		if (key === searchTerm)
			return obj[key];

		const result = _dfs(obj[key], searchTerm);
		if (result !== NOT_FOUND)
			return result;
	}
	return NOT_FOUND;
};


const getResultList = (jsonObj, options = {}) => {
	const {searchTerm = '_results'} = options;
	const resultList = _dfs(jsonObj, searchTerm);
	if (_isArray(resultList))
		return _toArray(resultList);
	else if (_isObject(resultList))
		return _toObject(resultList);
	else
		return resultList;
};

const getResult = (jsonObj, options) => {
	const resultList = getResultList(jsonObj, options);
	if (resultList)
		return resultList[0] || resultList;
	else if (resultList !== NOT_FOUND)
		return resultList;
	else
		return false;
};

const query = tableName => {
	const _generateParamsDeclaration = items => {
		const params = items.reduce((result, field) => {
			return `${result}, $${field}: String!`;
		}, '');
		return params.substr(1);
	};
	const _generateParams = items => {
		const params = items.reduce((result, field) => {
			return `${result}, ${field}:$${field}`;
		}, '');
		return params.substr(1);
	};
	const _generateFields = items => {
		const fields = items.reduce((result, field) => {
			//Handle dotWalk
			const temp= field.split('.');
			if (temp.length === 2)
				return `${result}
						${temp[0]}{
								_reference {
									${temp[1]} {
										value
									}
								}
						}`;
			else
				return `${result}
			       		${field}{
			         		value
			    		}`;
		}, '');
		return fields;
	};

	const get = (items, hasQuery, limit) => {
		const index = cuid();
		const pagination = limit ? `pagination: {limit: ${limit}}` : '';
		const queryConditionsHeader = hasQuery ? '($queryConditions:String!)' : '';
		const queryConditions = hasQuery ? 'queryConditions: $queryConditions, ' : '';
		const queryOptions = (hasQuery || limit) ?`(${queryConditions} ${pagination} )` : '';
		const fields = _generateFields(items);

		const query = `
			query GET_${tableName}_${index} ${queryConditionsHeader}{
			 GET_${tableName}_${index}: GlideRecord_Query {
				${tableName} ${queryOptions}{
				  _results {
					${fields}
				  }
				}
			  }
			}`;

		const actions = {
			invokeActionType: `${tableName.toUpperCase()}_${index}_GET_INVOKED`,
			successActionType: `${tableName.toUpperCase()}_${index}_GET_SUCCEEDED`,
			errorActionType: `${tableName.toUpperCase()}_${index}_GET_FAILED`
		};

		return {
			effect: createGraphQLEffect(query, {
				variableList: ['queryConditions'],
				...actions
			}),
			actions,
			query
		};
	};
	const insert = items => {
		const index = cuid();
		const paramsDeclaration = _generateParamsDeclaration(items);
		const params = _generateParams(items);
		const fields = _generateFields(items);

		const query = `
			mutation INSERT_${tableName}_${index} (${paramsDeclaration}){
			 INSERT_${tableName}_${index}: GlideRecord_Mutation {
				_results: insert_${tableName} (${params}) {
				     sys_id {
       					value
      				}
					${fields}
				}
			  }
			}`;

		const actions = {
			invokeActionType: `${tableName.toUpperCase()}_${index}_INSERT_INVOKED`,
			successActionType: `${tableName.toUpperCase()}_${index}_INSERT_SUCCEEDED`,
			errorActionType: `${tableName.toUpperCase()}_${index}_INSERT_FAILED`
		};
		return {
			effect: createGraphQLEffect(query, {
				variableList: items,
				...actions
			}),
			actions,
			query
		};
	};

	const update = (items) => {
		const index = cuid();
		const paramsDeclaration = _generateParamsDeclaration(items);
		const params = _generateParams(items);
		const fields = _generateFields(items);

		const query = `
			mutation UPDATE_${tableName}_${index} (${paramsDeclaration}){
			 UPDATE_${tableName}_${index}: GlideRecord_Mutation {
				_results: update_${tableName} (${params}) {
					${fields}
				}
			  }
			}`;

		const actions = {
			invokeActionType: `${tableName.toUpperCase()}_${index}_UPDATE_INVOKED`,
			successActionType: `${tableName.toUpperCase()}_${index}_UPDATE_SUCCEEDED`,
			errorActionType: `${tableName.toUpperCase()}_${index}_UPDATE_FAILED`
		};
		return {
			effect: createGraphQLEffect(query, {
				variableList: items,
				...actions
			}),
			actions,
			query
		};
	};
	return {
		get,
		insert,
		update
	};
};

export const gqlUtils = {
	NOT_FOUND,
	getResult,
	getResultList,
	query
};
