import {createCustomElement} from '@servicenow/ui-core';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import {view} from './snEmMetricHeaderView';
import styles from './snEmMetricHeader.scss';
import {headerActions} from './actions/headerActions';

createCustomElement('sn-em-metric-header', {
	view,
	styles,
	properties: {
		ciName: {
			default: undefined,
			schema: {type: 'string'}
		},
		lastRefreshedTimeAgo: {
			schema: {type: 'string'}
		}
	},
	actionHandlers: {
		...headerActions.actionHandlers
	}
});
