import {t} from 'sn-translate';

import {buildMetricsTree} from './transformToMetric';
import {TAB} from '../components/sn-em-metric-navigator-tabs/consts';

const I18N = {
	CI: {
		RECOMMENDED_METRICS: t('Featured metrics'),
		ADDITIONAL_METRICS: t('Additional metrics')
	}
};

const buildCITree = (ciSysId, ciName, data) => {
	const recommended = buildMetricsTree(data.favourite_metrics, ciSysId, ciName);
	const additional = buildMetricsTree(data.additional_metrics, ciSysId, ciName);

	const items = [{
		id: ciSysId,
		label: ciName,
		identifierProps: {type: 'icon', icon: 'configuration-item-outline'},
		children: [{
			id: 'recommended',
			label: I18N.CI.RECOMMENDED_METRICS,
			children: recommended.metrics
		}, {
			id: 'additional',
			label: I18N.CI.ADDITIONAL_METRICS,
			children: additional.metrics
		}]
	}];

	const mapping = new Map([...recommended.mapping, ...additional.mapping]);
	return {
		items,
		mapping
	};
};

const generateCISelectedItems = (ciSysId, tree, chartMetadata, treeSelectionChanged) => {
	if (treeSelectionChanged && treeSelectionChanged[TAB.CI]) {
		return treeSelectionChanged[TAB.CI].selectedItems;
	} else {
		const selectedItems = [];
		const prefix = [ciSysId, 'recommended'];
		if (chartMetadata && chartMetadata.metrics) {
			const metrics = buildMetricsTree(chartMetadata.metrics).metrics;
			metrics.forEach(metric => selectedItems.push([...prefix, metric.id]));
		}
		return selectedItems;
	}
};

const generateCIExpandedItems = (ciSysId, selectedItems, isFilterExist = false) => {
	const items = new Set();

	if (Array.isArray(selectedItems) && selectedItems.length > 0) {
		selectedItems.forEach(item => {
			items.add([item[0]]);
			items.add([item[0], item[1]]);
		});
	}
	//add default
	items.add([ciSysId]);
	items.add([ciSysId, 'recommended']);

	if (isFilterExist) items.add([ciSysId, 'additional']);

	return Array.from(items);
};

export {
	buildCITree,
	generateCISelectedItems,
	generateCIExpandedItems
};
