import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createLog} from '../../../../utils/logger';
import {chartActions} from './chartActions';
import {ENDPOINTS} from '../consts';

const LOG = createLog('dashboardActions');

const ACTION_TYPES = {
	DASHBOARD_FETCH_STARTED: 'DASHBOARD_FETCH#STARTED',
	DASHBOARD_FETCH_SUCCEEDED: 'DASHBOARD_FETCH#SUCCEEDED',
	...chartActions.ACTION_TYPES,
	METRICS_DATA_INITIAL_FETCH_REQUESTED: 'METRICS_DATA_INITIAL_FETCH#REQUESTED'
};

const dashboardFetchData = createHttpEffect(ENDPOINTS.DASHBOARD_ENDPOINT, {
	method: 'GET',
	queryParams: ['sys_id', 'sys_class_name'],
	successActionType: ACTION_TYPES.DASHBOARD_FETCH_SUCCEEDED,
	errorActionType: ACTION_TYPES.CHART_FETCH_FAILED
});

/**
 * Current implementation is a Naive one
 * We only filter out resources related to certain metric name if it passes the maxNumberOfMetrics
 * @param metricsData
 * @param maxNumberOfMetrics
 * @returns {{metrics}|*|{metrics: *}}
 * @private
 */
const _limitNumberOfSelectedMetrics = (metricsData, maxNumberOfMetrics) => {
	if (metricsData && metricsData.metrics) {
		const {metrics} = metricsData;
		if (Array.isArray(metrics)) {
			const newMetrics = metrics.map ( metric => {
				const {resources_id: resources} = metric;
				const keys = Object.keys(resources);

				// take the first 10 resources if we've passed maxNumberOfMetrics
				const newKeys = (keys.length > maxNumberOfMetrics) ? keys.slice(0, maxNumberOfMetrics) : keys;
				const resources_id = {};
				newKeys.forEach(key => resources_id[key] = resources[key]);

				return {
					...metric,
					resources_id
				}
			})
			return {
				...metricsData,
				metrics: newMetrics
			};
		}
	}
	return metricsData;
}

const dashboardFetchDataSucceeded = coeffects => {
	LOG.info('dashboardFetchDataSucceeded');
	const {action, properties, updateProperties, dispatch} = coeffects;
	const {payload: {result}} = action;
	const {
		ciSysId, chartIndex, startTime, endTime,
	} = properties;
	const {charts, charts_per_widget} = result;

	// limit number of metrics based on chart_per_widget value ( need to move inside API)
	const chartData = _limitNumberOfSelectedMetrics(charts[chartIndex], charts_per_widget);

	// TODO: Temporary solution for displaying only one item - needs to implemant a better API
	const chartMetadata = {
		...chartData,
		charts_per_widget
	};

	chartMetadata.metrics.forEach(metric => metric.ci_sys_id = ciSysId);

	updateProperties({
		chartMetadata
	});

	const {metrics} = chartMetadata;
	const payload = {ciSysId, startTime, endTime, metrics};

	dispatch(ACTION_TYPES.METRICS_DATA_INITIAL_FETCH_REQUESTED, payload);
};

const actionHandlers = {
	[ACTION_TYPES.DASHBOARD_FETCH_STARTED]: dashboardFetchData,
	[ACTION_TYPES.DASHBOARD_FETCH_SUCCEEDED]: dashboardFetchDataSucceeded,
	...chartActions.actionHandlers
};

const actions = {
	[ACTION_TYPES.DASHBOARD_FETCH_STARTED]: {},
	[ACTION_TYPES.DASHBOARD_FETCH_SUCCEEDED]: {},
	...chartActions.actions
};

export const dashboardActions = {
	ACTION_TYPES,
	actionHandlers,
	actions
};
