import '@servicenow/now-tabs';
import '@servicenow/now-heading';
import {t} from 'sn-translate';

import {createLog} from '../../../../../utils/logger';
import '../sn-em-metric-tree-of-metrics';
import { TAB } from './consts';


const LOG = createLog('snEmMetricNavigatorTabsView');
const I18N = {
	METRICS_ON_CI: t('Metrics on this CI'),
	METRICS_ON_HOST: t('Metrics on other CIs from the same host'),
	METRICS_ON_SERVICE: t('Metrics from Impacted services'),
	UNSELECT_ALL_METRICS: t('Unselect all')
};

const createHeaderJsx = (header, selectedItems) => {
	const isUnselectActionDisabled = !selectedItems.length;

	return (
		<div className='header'>
			<h5>{header}</h5>
			<now-button
				label={I18N.UNSELECT_ALL_METRICS}
				size='sm'
				bare
				disabled={isUnselectActionDisabled}
			/>
		</div>
	);
};

const createCITree = state => {
	const {properties: {treeExplorer, searchFilter}} = state;
	const {items, selectedItems, expandedItems} = treeExplorer.ci;

	return (
		<div>
			{createHeaderJsx(I18N.METRICS_ON_CI, selectedItems)}
			<sn-em-metric-tree-of-metrics
				name={TAB.CI}
				items={items}
				selected-items={selectedItems}
				expanded-items={expandedItems}
				search-filter={searchFilter}
			/>
		</div>
	);
};

const createHostJsx = state => {
	const {properties: {treeExplorer, searchFilter}} = state;
	const {items, selectedItems, expandedItems} = treeExplorer.host;

	return (
		<div>
			{createHeaderJsx(I18N.METRICS_ON_HOST, selectedItems)}
			<sn-em-metric-tree-of-metrics
				name={TAB.HOST}
				items={items}
				selected-items={selectedItems}
				expanded-items={expandedItems}
				search-filter={searchFilter}
			/>
		</div>
	);

};
const createServicesJsx = state => {
	const {properties: {treeExplorer, searchFilter}} = state;
	const {items, selectedItems, expandedItems} = treeExplorer.service;
	return (
		<div>
			{createHeaderJsx(I18N.METRICS_ON_SERVICE, selectedItems)}
			<sn-em-metric-tree-of-metrics
				name={TAB.SERVICE}
				items={items}
				selected-items={selectedItems}
				expanded-items={expandedItems}
				search-filter={searchFilter}
			/>
		</div>
	);

};

const createTabsJsx = state => {
	const {properties: {treeExplorer}, selectedTab} = state;
	const {
		ci: {count: ciCount},
		host: {count: hostCount},
		service: {count: serviceCount}} = treeExplorer;
	return (
		<now-tabs
			fixedWidth={true}
			items={[{
				'id': TAB.CI,
				'label': I18N.METRICS_ON_CI,
				'icon': 'star-outline',
				'count': ciCount,
			}, {
				'id': TAB.HOST,
				'label': I18N.METRICS_ON_HOST,
				'icon': 'serverbox-outline',
				'count': hostCount
			}, {
				'id': TAB.SERVICE,
				'label': I18N.METRICS_ON_SERVICE,
				'icon': 'tree-view-long-outline',
				'count': serviceCount
			}]}
			selectedItem={selectedTab}
			hide-label
		/>
	);
};

export const view = state => {
	const {selectedTab} = state;
	LOG.info('Rendering tabs', selectedTab);

	let tabContent;
	switch (selectedTab) {
	case TAB.HOST:
		tabContent = createHostJsx(state);
		break;
	case TAB.SERVICE:
		tabContent = createServicesJsx(state);
		break;
	case TAB.CI:
	default:
		tabContent = createCITree(state);
		break;
	}

	return (
		<div className='navigator-tabs'>
			{createTabsJsx(state)}
			{tabContent}
		</div>
	);
};
