import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-popover';
import '@servicenow/now-typeahead';
import {t} from 'sn-translate';
import '@servicenow/now-alert';
import './components/sn-em-metric-navigator-tabs';
import {createLog} from '../../../utils/logger';

const LOG = createLog('snEmMetricNavigatorPanelView');
const I18N = {
	PANEL_TITLE: t('Explore'),
	EXPLORE_TITLE: t('Compare and correlate metrics from the current CI and related CIs to understand the broader context of a problem'),
	MAX_METRICS_HAS_REACHED_MSG: t('You\'ve reached the max selection. You can select up to 10 metrics to display'),
	SEARCH: {
		placeholder: t('Search CI class, CI name, or metric name')
	}
};

export const view = state => {
	const {treeExplorer, search: {searchItems, searchFilter}, properties} = state;

	const {isShowNotification} = state;
	const {collapsed} = properties;
	const className = collapsed ? 'collapsed' : '';
	const nowAlertClassName = isShowNotification ? 'show': '';

	LOG.info('Rendering metric navigator panel', treeExplorer);

	return (
		<nav className={className}>
			<header>
				<now-heading
					label={I18N.PANEL_TITLE}
					variant="header-secondary"
					has-no-margin
					wont-wrap
				/>
				<now-popover
					hide-tail
					positions={[{target: 'center-end', content: 'center-start'}]}>
					<now-button-iconic
						bare={true}
						variant='tertiary'
						slot='trigger'
						size='md'
						icon='circle-info-outline'/>
					<sn-em-metric-navigator-panel-popover-content
						slot='content'
						content={I18N.EXPLORE_TITLE}>
					</sn-em-metric-navigator-panel-popover-content>
				</now-popover>
			</header>
			<now-alert
				className={nowAlertClassName}
				status="info"
				icon="info-circle-outline"
				content={I18N.MAX_METRICS_HAS_REACHED_MSG}
				action={{'type':'dismiss'}}/>
			<section className="search">
				<now-typeahead
					className="search-typeahead"
					items={searchItems}
					search="contains"
					placeholder={I18N.SEARCH.placeholder}
				/>
			</section>
			<section className="tree">
				<sn-em-metric-navigator-tabs
					tree-explorer={treeExplorer}
					search-filter={searchFilter}
				/>
			</section>
		</nav>
	);
};

