import {buildMetricsTree, breakItemToParts} from './transformToMetric';
import {TAB} from '../components/sn-em-metric-navigator-tabs/consts';

const REMOVE_NODE_FROM_TREE = {
	REMOVE_CI: 'REMOVE_CI',
	REMOVE_CLASS_TYPE: 'REMOVE_CLASS_TYPE'
};

const _buildHostCIs = (type, filter) => {
	const cItems = (type && type.items) ? type.items : [];
	return cItems.map(  ci => {
		const payload = {
			id: ci.ci_sys_id,
			label: ci.ci_name,
			identifierProps: {type: 'icon', icon: 'configuration-item-outline'}
		};

		const children = buildMetricsTree(ci.metrics, ci.ci_sys_id, ci.ci_name).metrics;
		if (filter.length){
			// if filtered provided , first filter metric names
			const filteredChildren = children.filter(item => item['metricTypeName'].toLowerCase().includes(filter));
			if (filteredChildren.length) {
				return {
					...payload,
					children: filteredChildren
				};
			} else if (ci.ci_name.toLowerCase().includes(filter) || type.ci_type.toLowerCase().includes(filter)){
				// if parent ci type is include in filter or the current ci name
				// include this ci with all the metrics beneath it
				return {
					...payload,
					children
				};
			} else {
				// if all metrics and ci are filtered
				// filter out the ci in that result
				return REMOVE_NODE_FROM_TREE.REMOVE_CI;
			}
		} else {
			return {
				...payload,
				children
			};
		}
	});
};

const _buildHostCITypes = (host, filter) => {
	const  types = (host && host.ci_types) ? host.ci_types : [];
	return types.map(type => {
		const payload = {
			id: type.ci_type,
			label: type.ci_type,
		};
		const children = _buildHostCIs(type, filter);
		if (filter.length) {
			const filteredChildren = children.filter(child	=> child !== REMOVE_NODE_FROM_TREE.REMOVE_CI);
			if (filteredChildren.length) {
				// return filtered CIs only
				return {
					...payload,
					children: filteredChildren
				};
			} else if (type.ci_type.toLowerCase().includes(filter)){
				return {
					...payload,
					children: []
				};
			} else {
				return REMOVE_NODE_FROM_TREE.REMOVE_CLASS_TYPE;
			}
		} else {
			return {
				...payload,
				children
			};
		}
	});
};

const buildHostTree = (host, searchFilter) => {
	const filter = searchFilter.toLowerCase();
	const children = _buildHostCITypes(host, filter);
	let filteredChildren;

	if (filter.length) {
		filteredChildren = children.filter(child => child !== REMOVE_NODE_FROM_TREE.REMOVE_CLASS_TYPE);
	} else {
		filteredChildren = children;
	}

	return [{
		id: host.host_sys_id,
		label: host.host_name,
		identifierProps: {type: 'icon', icon: 'serverbox-outline'},
		children: filteredChildren
	}];
};

const generateHostSelectedItems = (tree, treeSelectionChanged) => {
	const selectedItems = (treeSelectionChanged && treeSelectionChanged[TAB.HOST])?
		treeSelectionChanged[TAB.HOST].selectedItems : [];
	return selectedItems;
};


const generateHostExpandedItems = (selectedItems = [], defaultSelection) => {
	const items = new Set();
	selectedItems.forEach( item => breakItemToParts([], items, [...item]) );

	if (defaultSelection) items.add(defaultSelection);
	return Array.from(items);
};

export {
	buildHostTree,
	generateHostExpandedItems,
	generateHostSelectedItems
};
