
export const isDevMode = () => {
	return process.env.NODE_ENV === 'development'; //eslint-disable-line no-undef
};


/**
 * This is internal sanity function that recursively freezes the given object.
 * Any attempt to modify any nested property on frozed object will throw an exception.
 * Use this function to protect against accidental mutability of your collections.
 * The function is turned off in PROD environment to eliminate any overhead. Means, use it and keep it in your code, it won't hurt.
 * @param data - an object to turn into immutable.
 * `data` will turn into immutable.
 * @return the input `data` object.
 *
 */
export const deepFreeze = data => {
	if (isDevMode()){
		if (!data || typeof data !== 'object')
			return data;

		const ownProps = Object.getOwnPropertyNames(data);
		ownProps.forEach(deepFreeze);
		return Object.freeze(data);
	}
	return data;
};
