import momentTz from 'moment-timezone';

export const getFormattedMonth = month => {
	return month+1 < 10 ? `0${month+1}` : month;
};

export const getFormattedDay = day => {
	return day < 10 ? `0${day}` : day;
};

export const getNumberWithTrailingZero = number => {
	return number < 10 ? `0${number}`: number;
};

const _convertToUTCString = (timeStamp, timeZoneOffsetMs) => {
	const date = new Date(timeStamp);
	const timeZoneOffsetInHours = timeZoneOffsetMs / (1000 * 60 * 60);

	date.setHours(date.getHours() + timeZoneOffsetInHours + date.getTimezoneOffset()/60);

	const year = date.getFullYear();
	const month = getFormattedMonth(date.getMonth());
	const dateNumber = getNumberWithTrailingZero(date.getDate());
	const hours = getNumberWithTrailingZero(date.getHours());
	const minutes = getNumberWithTrailingZero(date.getMinutes());
	const seconds = getNumberWithTrailingZero(date.getSeconds());

	return {
		date: `${year}-${month}-${dateNumber}`,
		time: `${hours}:${minutes}:${seconds}`
	};
};

export const convertStartAndEndTimes = (startTime, endTime, zone) => {
	const startTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, startTime);
	const endTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, endTime);
	const formattedStart = _convertToUTCString(startTime, startTimeOffset);
	const formattedEnd = _convertToUTCString(endTime, endTimeOffset);

	return {
		formattedStart,
		formattedEnd
	};
};

export const convertTimeToRelativeServerTime = (time, offset) => {
	const date = new Date(time);
	const localOffsetInMs = date.getTimezoneOffset() * 60 * 1000; //convert to Ms

	// convert to GMT and use timezone
	return date.getTime() + localOffsetInMs + offset;
};

export const calculateTimeStampUsingTimeZone = (timestamp, timeZoneOffset) => {
	const localTime = new Date(timestamp);
	const localOffsetMs = localTime.getTimezoneOffset() * 60 * 1000; // convert to ms

	return timestamp + (-1 * localOffsetMs) + (-1 * timeZoneOffset);
};

export const getStartAndEndTimesForPreset = timePreset => {
	const start = new Date();
	start.setHours(start.getHours() + (parseInt(timePreset) * -1));

	return {
		start: start.getTime(),
		end: Date.now()
	};
};
export const getOffsetBasedOnTimeAndZoneInMin =
		(zone = 'America/Los_Angeles', timestamp) => momentTz(timestamp).tz(zone).utcOffset();

export const getOffsetBasedOnTimeAndZoneInMs =
	(zone, timestamp) => getOffsetBasedOnTimeAndZoneInMin(zone, timestamp) * 60 * 1000;


