import '@servicenow/now-content-tree';

import {createLog} from '../../../../../utils/logger';

const LOG = createLog('snEmMetricTreeOfMetricsView');

export const view = state => {
	const {
		properties: {
			items,
			selectedItems,
			expandedItems,
			searchFilter
		}
	} = state;

	LOG.info('Rendering metrics tree');

	return (
		<now-content-tree
			items={items}
			selected-items={selectedItems}
			expanded-items={expandedItems}
			loading-items={[]}
			show-presence-pattern
			manage-selected-items
			trigger-icon="chevron"
			overflow="truncate"
			select="multi"
			size="md"
			search-term={searchFilter}
		/>
	);
};
