import {createLog} from '../../../../utils/logger';

const LOG = createLog('headerActions');

const ACTION_TYPES = {
	REFRESH_DATA_REQUESTED: 'REFRESH_DATA#REQUESTED'
};

const handleRefreshButtonClick = coeffects => {
	LOG.log('handleRefreshButtonClick fired');
	const {action, dispatch} = coeffects;

	action.stopPropagation();
	dispatch([ACTION_TYPES.REFRESH_DATA_REQUESTED]);
};

const actionHandlers = {
	'NOW_BUTTON_ICONIC#CLICKED': handleRefreshButtonClick
};

export const headerActions = {
	ACTION_TYPES,
	actionHandlers
};
