import {createLog} from '../../../../utils/logger';
import {gqlUtils} from '../../../../utils/graphQueryUtils';

const LOG = createLog('ciActions');

const gqlQuery = gqlUtils.query('cmdb_ci');
const GQL = {
	GET: gqlQuery.get(['name'], true)
};

const ACTION_TYPES = {
	CI_NAME_REQUESTED: 'CI_NAME#REQUESTED',
	CI_NAME_INVOKED: 'CI_NAME#INVOKED'
};

const ciNameRequested = coeffects => {
	LOG.log('ciActions -> ciNameRequested');
	const {action: {payload}, dispatch} = coeffects;
	const {ciSysId} = payload;

	const queryConditions = `sys_id=${ciSysId}`;

	dispatch([ACTION_TYPES.CI_NAME_INVOKED], {queryConditions});
};

const getCiNameSucceeded = coeffects => {
	LOG.log('ciActions -> getCiNameSucceeded');
	const {action: {payload}, updateProperties} = coeffects;

	const result = gqlUtils.getResult(payload);

	LOG.info('ciActions -> getCiNameSucceeded -> result', result);

	const {name: ciName} = result;

	updateProperties({
		ciName
	});
};

const getCiNameFailed = coeffects => {
	LOG.error('ciActions -> getCiNameFailed', coeffects);
};

const actions = {
	[ACTION_TYPES.CI_NAME_REQUESTED]: {
		schema: {
			properties: {
				ciSysId: {type: 'string', pattern: '^[[0-9a-fA-F]{32}$'}
			},
			required: ['ciSysId']
		}
	}
};

const actionHandlers = {
	[ACTION_TYPES.CI_NAME_REQUESTED]: ciNameRequested,
	[ACTION_TYPES.CI_NAME_INVOKED]: GQL.GET.effect,
	[GQL.GET.actions.successActionType]: getCiNameSucceeded,
	[GQL.GET.actions.errorActionType]: getCiNameFailed
};

export const ciActions = {
	ACTION_TYPES,
	actions,
	actionHandlers
};
