import {t} from 'sn-translate';
import {createLog} from '../../../../utils/logger';
import {gqlUtils} from '../../../../utils/graphQueryUtils';
import {getOffsetBasedOnTimeAndZoneInMs} from '../../../../utils/dateUtils';
import {convertStartAndEndTimes} from '../../../../utils/dateUtils';
import momentTz from 'moment-timezone';

const LOG = createLog('alertAnnotationsActions');

const gqlQuery = gqlUtils.query('em_alert');
const GQL = {
	GET: gqlQuery.get(['cmdb_ci', 'number', 'state', 'initial_remote_time'], true, 1000)
};

const ACTION_TYPES = {
	ALERT_ANNOTATION_REQUESTED: 'ALERT_ANNOTATION#REQUESTED',
	ALERT_ANNOTATION_INVOKED: GQL.GET.actions.invokeActionType
};

const prepareAlertsAnnotationQuery = coeffects => {
	LOG.info('prepareAlertsAnnotationQuery');

	const {state, dispatch} = coeffects;
	const {properties: {startTime, endTime, ciSysId, timeConfig, datePickerChanged}} = state;
	const {zone} = timeConfig;

	let startTimestamp = startTime, endTimestamp = endTime;

	// if changed, start and end times will be browser's local time
	// so we change it to match the values on the date picker
	if (datePickerChanged) {
		const start = new Date(startTime);
		let localBrowserOffsetInMs = start.getTimezoneOffset() * 60 * 1000;
		const timeOfStart = start.getTime();
		startTimestamp = startTime + (-1 * localBrowserOffsetInMs) + (-1 * getOffsetBasedOnTimeAndZoneInMs(zone, timeOfStart));

		const end = new Date(endTimestamp);
		localBrowserOffsetInMs = end.getTimezoneOffset() * 60 * 1000;
		const timeOfEnd = end.getTime();
		endTimestamp = endTime + (-1 * localBrowserOffsetInMs) + (-1 * getOffsetBasedOnTimeAndZoneInMs(zone, timeOfEnd));
	}

	const {formattedStart, formattedEnd} = convertStartAndEndTimes(startTimestamp, endTimestamp, zone);

	const queryConditions = `cmdb_ci=${ciSysId}^initial_remote_timeBETWEENjavascript:gs.dateGenerate('${formattedStart.date}','${formattedStart.time}')@javascript:gs.dateGenerate('${formattedEnd.date}','${formattedEnd.time}')`;
	dispatch(ACTION_TYPES.ALERT_ANNOTATION_INVOKED, {queryConditions});
};

const _getAnnotationsData = (results, ciName, timeConfig) => {
	const {zone} = timeConfig;

	return results.map(item => {
		const {number, initial_remote_time} = item;
		// get time in UTC timestamp
		const initialRemoteTime = momentTz.utc(initial_remote_time).valueOf();

		// get offset in milliseconds
		const offsetInMs = getOffsetBasedOnTimeAndZoneInMs(zone, initialRemoteTime);

		const x = initialRemoteTime + offsetInMs;

		return {
			type: 'comment',
			message: `${number} ${t('opened')} for ${ciName}`,
			x
		};
	});
};

const _getAnnotationConfig = () => {
	return {
		comment: {
			symbol: 'exclamation'
		}
	};
};

const getAlertStateSucceeded = coeffects => {
	LOG.info('getAlertStateSucceeded');
	const {action: {payload}, state, properties, updateProperties, updateState} = coeffects;
	const {ciName, timeConfig} = properties;

	const resultList = gqlUtils.getResultList(payload);

	const {properties: {chartConfig}} = state;

	const annotationsConfig = _getAnnotationConfig();
	const annotationsData = _getAnnotationsData(resultList, ciName, timeConfig);

	const chartConfigWithAnnotations = {
		...chartConfig,
		annotationsConfig,
		annotationsData
	};

	updateState({
		annotationsConfig,
		annotationsData
	});

	LOG.info('getAlertStateSucceeded chartConfigWithAnnotations', chartConfigWithAnnotations);
	updateProperties({
		chartConfig: chartConfigWithAnnotations
	});
};

const fetchFailed = () => {
	LOG.info('alertAnnotationsActions fetchFailed');
};

const actions = {
	[ACTION_TYPES.ALERT_ANNOTATION_REQUESTED]: {
		schema: {type: 'object'}
	}
};

const actionHandlers = {
	[ACTION_TYPES.ALERT_ANNOTATION_REQUESTED]: prepareAlertsAnnotationQuery,
	[ACTION_TYPES.ALERT_ANNOTATION_INVOKED]: GQL.GET.effect,
	[GQL.GET.actions.successActionType]: getAlertStateSucceeded,
	[GQL.GET.actions.errorActionType]: fetchFailed
};

export const alertAnnotationsActions = {
	ACTION_TYPES,
	actions,
	actionHandlers
};
