import {metricActions} from '../../../../../actions/metricActions';
import {treeConstants} from '../../../../../constants/tree';

const OPERATION = {
	'REMOVE_CHART': 0,
	'ADD_CHART': 1
};

const addOrRemoveChart = (currentItem, selectedItems) => {
	for (let item of selectedItems) {
		// if leaf id can be found on selected items, operation should be remove
		if (item[item.length -1] === currentItem.id) {
			return OPERATION.REMOVE_CHART;
		}
	}
	return OPERATION.ADD_CHART;
};

const treeItemClicked = coeffects => {
	const {action, dispatch, state: { properties}} = coeffects;
	const { payload: { item }} = action;

	action.stopPropagation();

	// if not leaf of tree, ignore click
	if (item && item.id && !item.id.startsWith(treeConstants.LEAF)) return;

	const { selectedItems } = properties;
	const operation = addOrRemoveChart(item, selectedItems);

	const payload = {
		metric_name: item.metricTypeName,
		metric_tiny_name: item.metricTypeTinyName,
		metric_type_id: item.metricTypeId,
		resources_id: item.resourceId,
		ci_sys_id: item.ciSysId,
		ci_name: item.ciName,
		resource_name: item.resourceName
	};

	if (operation) {
		// Add requested
		dispatch(metricActions.ACTION_TYPES.METRIC_ADD_REQUESTED, {
			...payload
		});
	} else {
		// Remove requested
		dispatch(metricActions.ACTION_TYPES.METRIC_REMOVE_REQUESTED, {
			...payload
		});

	}
};

const treeItemSelected = coeffects => {
	const {action, state, updateProperties, dispatch} = coeffects;
	const { properties: { selectedItems, name }} = state;
	const { payload: { value }} = action;

	action.stopPropagation();
	const items = (selectedItems instanceof Object) ? selectedItems : [];
	if (items.length < value.length) {
		// add to selection
		const addedItem = value.pop();

		if (addedItem[addedItem.length -1].startsWith(treeConstants.LEAF)) {
			// allow selection only if it is a leaf
			const newSelectedItems = [...items, addedItem];
			updateProperties({
				selectedItems: newSelectedItems
			});
			dispatch(metricActions.ACTION_TYPES.TREE_SELECTION_CHANGED, {
				name,
				selectedItems: newSelectedItems
			});
		}
	} else {
		// subtract from selection
		updateProperties({
			selectedItems: value
		});
		dispatch(metricActions.ACTION_TYPES.TREE_SELECTION_CHANGED, {
			name,
			selectedItems: value
		});
	}
};

const actionHandlers = {
	'NOW_CONTENT_TREE#ITEM_CLICKED': treeItemClicked,
	'NOW_CONTENT_TREE#SELECTED_ITEMS_SET': treeItemSelected
};

export const metricsTreeActions = {
	actionHandlers
};
