import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createLog} from '../../../../utils/logger';
import {ENDPOINTS} from '../consts';
import {isEmpty} from '../../../../utils/objUtils';

const LOG = createLog('metricDataActions');

export const getEntitiesStructure = () => {
	return {
		metrics: new Set(),
		ci: new Set(),
		class: new Set()
	};
};


const ACTION_TYPES = {
	METRIC_CI_FETCH_STARTED: 'METRIC_CI_FETCH#STARTED',
	METRIC_CI_FETCH_SUCCEEDED: 'METRIC_CI_FETCH#SUCCEEDED',
	METRIC_CI_FETCH_FAILED: 'METRIC_CI_FETCH#FAILED',
	METRIC_HOST_FETCH_STARTED: 'METRIC_HOST_FETCH#STARTED',
	METRIC_HOST_FETCH_SUCCEEDED: 'METRIC_HOST_FETCH#SUCCEEDED',
	METRIC_HOST_FETCH_FAILED: 'METRIC_HOST_FETCH#FAILED',
	METRIC_SERVICE_FETCH_STARTED: 'METRIC_SERVICE_FETCH#STARTED',
	METRIC_SERVICE_FETCH_SUCCEEDED: 'METRIC_SERVICE_FETCH#SUCCEEDED',
	METRIC_SERVICE_FETCH_FAILED: 'METRIC_SERVICE_FETCH#FAILED'
};

const metricCIFetchData = createHttpEffect(ENDPOINTS.METRIC_CI_ENDPOINT, {
	method: 'GET',
	pathParams: ['ciSysId'],
	successActionType: ACTION_TYPES.METRIC_CI_FETCH_SUCCEEDED,
	errorActionType: ACTION_TYPES.METRIC_CI_FETCH_FAILED
});

const metricHostFetchData = createHttpEffect(ENDPOINTS.METRIC_HOST_ENDPOINT, {
	method: 'GET',
	pathParams: ['ciSysId'],
	successActionType: ACTION_TYPES.METRIC_HOST_FETCH_SUCCEEDED,
	errorActionType: ACTION_TYPES.METRIC_HOST_FETCH_FAILED
});

const metricServiceFetchData = createHttpEffect(ENDPOINTS.METRIC_SERVICE_ENDPOINT, {
	method: 'GET',
	pathParams: ['ciSysId'],
	successActionType: ACTION_TYPES.METRIC_SERVICE_FETCH_SUCCEEDED,
	errorActionType: ACTION_TYPES.METRIC_SERVICE_FETCH_FAILED
});

const _getMetricForCINames = data => {
	LOG.info('_getMetricForCINames started');

	const entities = getEntitiesStructure();
	data.favourite_metrics.forEach(i => entities.metrics.add(i['metric_type']));
	data.additional_metrics.forEach(i => entities.metrics.add(i['metric_type']));

	LOG.info('_getMetricForCINames ended', entities);
	return entities;
};

const _getCINamesOnHost = host => {
	const entities = getEntitiesStructure();
	host.ci_types.forEach(type => {
		entities.class.add(type.ci_type);
		type.items.forEach(item => {
			entities.ci.add(item.ci_name);
			item.metrics.forEach(metric => {
				entities.metrics.add(metric.metric_type);
			});
		});
	});
	return entities;
};

const _getMetricOnHostEntityNames = host => {
	const ciClassHostAndMetricNames = _getCINamesOnHost(host);
	return ciClassHostAndMetricNames;
};

const _getMetricOnServiceEntityNames = data => {
	const entities = getEntitiesStructure();

	data.forEach(service => {
		service.entries.forEach(entry => {
			entry.metrics.forEach( metric => {
				entities.metrics.add(metric['metric_type']);
			});
		});
		service.additional.forEach(metric => {
			entities.metrics.add(metric['metric_type']);
		});
	});
	return entities;
};

const metricCIFetchDataSucceeded = coeffects => {
	LOG.info('metricCIFetchDataSucceeded');

	const {updateProperties, properties: {ciSysId}} = coeffects;
	const {action: {payload}} = coeffects;
	const {result: {data}} = payload;
	const metrics = _getMetricForCINames(data).metrics;

	const newProperties = {
		metricsCIData: {
			ci: {
				ciSysId,
				data
			},
			entities: {
				metrics
			}
		}
	};

	LOG.info('metricCIFetchDataSucceeded {metricsCIData, entities}', newProperties);
	updateProperties(newProperties);
};

const metricHostFetchDataSucceeded = coeffects => {
	LOG.info('metricHostFetchDataSucceeded');

	const {action, updateProperties} = coeffects;
	const {payload: {result: {host}}} = action;
	const data = isEmpty(host)? { ci_types: []} : host;
	const hostEntities = _getMetricOnHostEntityNames(data);

	const newProperties = {
		metricsHostData: {
			host: {
				data
			},
			entities: {
				ci: hostEntities.ci,
				class: hostEntities.class,
				metrics: hostEntities.metrics
			}
		}
	};
	LOG.info('metricHostFetchDataSucceeded metricsData', newProperties);
	updateProperties(newProperties);
};

const metricServiceFetchDataSucceeded = coeffects => {
	LOG.info('metricServiceFetchDataSucceeded');

	const {updateProperties} = coeffects;
	const {action: {payload}} = coeffects;
	const {result: {data}} = payload;

	const propertyPayload = (data, metrics) => (
		{
			metricsServiceData: {
				service: {
					data
				},
				entities: {
					metrics
				}
			}
		}
	);

	let newProperty;
	if (Array.isArray(data) && (data[0] === null || isEmpty(data[0]))) {
		newProperty = 	propertyPayload([],[]);
	} else {
		const metrics = _getMetricOnServiceEntityNames(data).metrics;
		newProperty = propertyPayload( data, metrics);

	}

	LOG.info('metricServiceFetchDataSucceeded metricsData', newProperty);
	updateProperties(newProperty);
};

const actionHandlers = {
	[ACTION_TYPES.METRIC_CI_FETCH_STARTED]: metricCIFetchData,
	[ACTION_TYPES.METRIC_CI_FETCH_SUCCEEDED]: metricCIFetchDataSucceeded,
	[ACTION_TYPES.METRIC_HOST_FETCH_STARTED]: metricHostFetchData,
	[ACTION_TYPES.METRIC_HOST_FETCH_SUCCEEDED]: metricHostFetchDataSucceeded,
	[ACTION_TYPES.METRIC_SERVICE_FETCH_STARTED]: metricServiceFetchData,
	[ACTION_TYPES.METRIC_SERVICE_FETCH_SUCCEEDED]: metricServiceFetchDataSucceeded
};

const actions = {
	[ACTION_TYPES.METRIC_CI_FETCH_STARTED]: {},
	[ACTION_TYPES.METRIC_CI_FETCH_SUCCEEDED]: {},
	[ACTION_TYPES.METRIC_HOST_FETCH_STARTED]: {},
	[ACTION_TYPES.METRIC_HOST_FETCH_SUCCEEDED]: {},
	[ACTION_TYPES.METRIC_SERVICE_FETCH_STARTED]: {},
	[ACTION_TYPES.METRIC_SERVICE_FETCH_SUCCEEDED]: {},
	[ACTION_TYPES.DASHBOARD_FETCH_STARTED]: {},
	[ACTION_TYPES.DASHBOARD_FETCH_SUCCEEDED]: {}
};

export const metricDataActions = {
	ACTION_TYPES,
	actionHandlers,
	actions
};
