import { TAB } from '../consts';
import {createLog} from '../../../../../../utils/logger';
import {treeConstants} from '../../../../../constants/tree';
import {metricActions} from '../../../../../actions/metricActions';

const LOG = createLog('navigatorTabsActions');

const tabSelected = coeffects => {
	const {action, updateState} = coeffects;
	const {payload: {value}} = action;

	action.stopPropagation();
	LOG.log(`tab selection changed ${value}`);
	updateState({selectedTab: value});
};

const _generateMetricPayload = (metric = {}, item) => ({
	item,
	metric_name: metric.metricTypeName,
	metric_tiny_name: metric.metricTypeTinyName,
	metric_type_id: metric.metricTypeId,
	resources_id: metric.resourceId,
	ci_sys_id: metric.ciSysId
});

const _getMetricsToRemoveCI = (selectedItems, mapping) => {
	const metrics = [];
	selectedItems.forEach(item => {
		const ciSysId = item[0];
		const metricId = item[item.length - 1];
		const metric = mapping.get(treeConstants.generateMapKey(ciSysId, metricId));
		if (metric)  metrics.push(_generateMetricPayload(metric, item));
	});
	return metrics;
};

const _getMetricsToRemoveHost = (selectedItems, mapping) => {
	const metrics = [];
	selectedItems.forEach(item => {
		const ciSysId = item[item.length - 2];
		const metricId = item[item.length - 1];
		const metric = mapping.get(treeConstants.generateMapKey(ciSysId, metricId));
		if (metric)  metrics.push(_generateMetricPayload(metric, item));
	});
	return metrics;
};

const _getMetricsToRemoveService = (selectedItems, mapping) => {
	const metrics = [];
	selectedItems.forEach(item => {
		const ciSysId = item[item.length - 2].endsWith('additional') ? item[0] : item[item.length - 2];
		const metricId = item[item.length - 1];
		const metric = mapping.get(treeConstants.generateMapKey(ciSysId, metricId));
		if (metric)  metrics.push(_generateMetricPayload(metric, item));
	});
	return metrics;
};

const _getMetricsToRemove = (selectedItems, mappings, selectedTab) => {
	switch(selectedTab) {
	case TAB.CI:
		return _getMetricsToRemoveCI(selectedItems, mappings);
	case TAB.HOST:
		return _getMetricsToRemoveHost(selectedItems, mappings);
	case TAB.SERVICE:
		return _getMetricsToRemoveService(selectedItems, mappings);
	}

};
const _getRelevantSection = (selectedTab, treeExplorer) => {
	let relevantSection = {};
	switch(selectedTab) {
	case TAB.CI:
		relevantSection = treeExplorer.ci;
		break;
	case TAB.HOST:
		relevantSection = treeExplorer.host;
		break;
	case TAB.SERVICE:
		relevantSection= treeExplorer.service;
		break;
	}

	return relevantSection;
};

const unselectAllClicked = coeffects => {
	const {state: {selectedTab}, dispatch} = coeffects;
	const {properties: {treeExplorer}} = coeffects;

	const {selectedItems, mapping} = _getRelevantSection(selectedTab, treeExplorer);
	const metrics = _getMetricsToRemove(selectedItems, mapping,selectedTab);
	dispatch(metricActions.ACTION_TYPES.METRICS_REMOVE_REQUESTED, {
		name: selectedTab,
		metrics
	});
};

const actionHandlers = {
	'NOW_TABS#SELECTED_ITEM_SET': tabSelected,
	'NOW_BUTTON#CLICKED': unselectAllClicked
};

export const navigatorTabsActions = {
	actionHandlers
};
