import {createLog} from '../../../../../../utils/logger';
import {get} from 'lodash';

const LOG = createLog('chartNavigatorActions');

const chartNavigatorDomainChanged = {
	//we use delay here as a workaround for the navigator problem on re-render
	modifier: {name: 'debounce', delay: 1000},
	effect: (coeffects) => {
		const {action: {payload}, updateState} = coeffects;
		const {value: selectedDomainValue} = payload;

		LOG.info('chartNavigatorDomainChanged-> changed chart domain: ', selectedDomainValue);

		if (!get(selectedDomainValue, 'x', false)) {
			return;
		}

		updateState({
			path: 'chartOptions.selectedDomain',
			value: selectedDomainValue,
			operation: 'assign'
		});
	}
};


const actionHandlers = {
	'NOW_CHART_NAVIGATOR#SELECTED_DOMAIN_CHANGED': chartNavigatorDomainChanged
};

export const chartNavigatorActions = {
	actionHandlers
};
