import {treeConstants} from '../../../constants/tree';
import {buildCITree, generateCISelectedItems, generateCIExpandedItems} from './transformToCI';
import {buildHostTree, generateHostSelectedItems, generateHostExpandedItems} from './transformToHost';
import {buildServicesTree, generateServiceSelectedItems, generateServiceExpandedItems} from './transformToService';

const extractMetricsInTree = (tree, metrics = [], mapping = new Map()) => {
	if (tree){
		const node = tree[0] ? tree[0] : tree;
		if (node.children){
			node.children.forEach(child => extractMetricsInTree(child, metrics, mapping));
		} else {
			if (node.id && node.id.startsWith(treeConstants.LEAF)){
				metrics.push(node);
				mapping.set(treeConstants.generateMapKey(node.ciSysId, node.id), node);
			}
		}
	}
	return {
		metrics,
		mapping};
};

const extractMetricsInForest = (forest, metrics = []) => {
	let allMappings = new Map();
	if (forest && forest.length > 0){
		forest.forEach(tree => {
			const mapping = new Map();
			extractMetricsInTree(tree, metrics, mapping);
			allMappings = new Map([...allMappings, ...mapping]);
		});
	}
	return {
		metrics,
		mapping: allMappings
	};
};

const expandAllUnderTree = (tree, expandedItems = [], prefix = []) => {
	if (tree) {
		const node = tree[0] ? tree[0] : tree;
		if (node.id) {
			if (node.id.startsWith(treeConstants.LEAF)) return;
			const newPrefix = [...prefix, node.id];
			expandedItems.push(newPrefix);
			if (node.children && node.children.length > 0) {
				node.children.forEach(child => expandAllUnderTree(child, expandedItems, newPrefix));
			}
		}
	}
	return expandedItems;
};

const expandAllInForest = ( forest, expandedItems = []) => {
	if (forest && forest.length > 0) {
		forest.forEach(tree => expandAllUnderTree(tree, expandedItems));
	}
	return expandedItems;
};

/**
 * Toggle if certain leaf (metric) is disable or enable based on disableNotSelected variable
 * we are indifference if disableNotSelected is true or false
 * @param node
 * @param selectedItems
 * @param disableNotSelected
 * @private
 */
const _toggleEnableOrDisableItems = (node, selectedItems, disableNotSelected) => {
	if (node.children) {
		_toggleEnableOrDisableItems(node.children, selectedItems, disableNotSelected);
	} else if (Array.isArray(node)) {
		node.forEach(node => _toggleEnableOrDisableItems(node, selectedItems, disableNotSelected));
	} else if (node.id) {
		if(!selectedItems.has(node.id)){
			node.disabled = disableNotSelected;
		}
	}
};

const enableItemsSelectionOnTree = (tree, selectedItems, disableNotSelected) => {
	if (Array.isArray(tree) && tree[0] &&
		tree[0].children && Array.isArray(selectedItems)){
		const selectedItemsSet = new Set();
		selectedItems.forEach(item => selectedItemsSet.add(item[item.length-1]));
		_toggleEnableOrDisableItems(tree[0].children, selectedItemsSet, disableNotSelected);
	}
};

const enableItemsSelectionOnForest = (forest, selectedItems, disableNotSelected) => {
	if (Array.isArray(forest)) {
		forest.forEach(tree => enableItemsSelectionOnTree(tree, selectedItems, disableNotSelected));
	}
};

export const transformToTree = {
	// CI helpers functions
	buildCITree,
	generateCISelectedItems,
	enableItemsSelectionOnCI: enableItemsSelectionOnTree,
	generateCIExpandedItems,
	// Host helpers functions
	buildHostTree,
	generateHostSelectedItems,
	generateHostExpandedItems,
	enableItemsSelectionOnHost: enableItemsSelectionOnTree,
	extractMetricsUnderHost: extractMetricsInTree,
	expandAllUnderHost: expandAllUnderTree,
	// Service helpers functions
	buildServicesTree,
	generateServiceSelectedItems,
	generateServiceExpandedItems,
	enableItemsSelectionOnService: enableItemsSelectionOnForest,
	extractMetricsUnderServices: extractMetricsInForest,
	expandAllUnderServices: expandAllInForest
};
