import moment from 'moment';
import {actionTypes} from '@servicenow/ui-core';
import {createLog} from '../../../../utils/logger';

const LOG = createLog('workAreaActions');

const {COMPONENT_PROPERTY_CHANGED} = actionTypes;
const ACTION_TYPES = {
	WORK_AREA_TOGGLED: 'WORK_AREA#TOGGLED',
	WORK_AREA_METRIC_DATE_CHANGED: 'WORK_AREA_METRIC_DATE#CHANGED',
	ANNOTATIONS_ON_CHART_TOGGLED: 'ANNOTATIONS_ON_CHART#TOGGLED',
	ALERT_STATE_FETCH_STARTED: 'ALERT_STATE_FETCH#STARTED',
	ALERT_STATE_FETCH_SUCCEEDED: 'ALERT_STATE_FETCH#SUCCEEDED',
	ALERT_STATE_FETCH_FAILED: 'ALERT_STATE_FETCH#FAILED'
};

const paneExpandedCollapsed = coeffects => {
	LOG.info('paneExpandedCollapsed');
	const {action, dispatch} = coeffects;
	action.stopPropagation();
	dispatch(ACTION_TYPES.WORK_AREA_TOGGLED);
};

const datePresetSelected = coeffects => {
	LOG.info('datePresetSelected');
	const {action, updateState, dispatch} = coeffects;
	const {payload: {value: preset}} = action;
	action.stopPropagation();
	updateState({preset});

	//Check that we are not on custom range
	let payload = {};
	if (preset && ~~preset) {
		const now = new Date().getTime();
		const end = now;
		const start = moment(now).add('-' + preset, 'hours').valueOf();
		payload = {
			start,
			end
		};
	} else {
		payload = {
			start: null,
			end: null
		};
	}

	payload.timePreset = preset;
	dispatch(ACTION_TYPES.WORK_AREA_METRIC_DATE_CHANGED, payload);
};

const dateRangePickerChanged = coeffects => {
	LOG.info('dateRangePickerChanged');
	const {action: {payload}, properties, dispatch} = coeffects;
	const {start, end} = payload;
	const {timeConfig: {date, time}} = properties;
	const dateFormat = `${date.toUpperCase()} ${time}`;

	// if start and date are equal - do not call dispatch
	// to prevent sending wrong dates to the server and getting 500 status error
	if (start === end) {
		return;
	}

	const newPayload = {
		start: moment(start, dateFormat).valueOf(),
		end: moment(end, dateFormat).valueOf()
	};

	LOG.info('dateRangePickerChanged-> newPayload', newPayload);
	dispatch(ACTION_TYPES.WORK_AREA_METRIC_DATE_CHANGED, newPayload);
};

const actions = {
	[ACTION_TYPES.WORK_AREA_TOGGLED]: {},
	[ACTION_TYPES.WORK_AREA_METRIC_DATE_CHANGED]: {
		schema: {
			type: 'object',
			properties: {
				start: {type: 'string', pattern: '^[0-9]+$'},
				end: {type: 'string', pattern: '^[0-9]+$'}
			},
			required: ['start', 'end']
		}
	}
};

const propertyHasChanged = coeffects => {
	const {action: {payload}, updateState} = coeffects;
	const {name, previousValue, value} = payload;

	switch(name){
		case 'chartIndex':
			LOG.info(`differentChartSelected': was ${previousValue} now ${value}`);
			updateState({
				showAnnotationsOnChart: false,
				preset: undefined,
			});
			break;
	}
};
const actionHandlers = {
	[COMPONENT_PROPERTY_CHANGED]: propertyHasChanged,
	'NOW_BUTTON_ICONIC#CLICKED': paneExpandedCollapsed,
	'NOW_SELECT#SELECTED_ITEM_SET': datePresetSelected,
	'NOW_DATE_RANGE_PICKER#VALUE_CHANGED': dateRangePickerChanged
};

export const workAreaActions = {
	ACTION_TYPES,
	actions,
	actionHandlers
};
