// return true if provided is an Object and it is empty
const isEmpty = object => object && Object.keys(object).length === 0 && object.constructor === Object;

// metric resources comes as an object but
// if no resources provided we will still get and object but with one key and one value  "":""
const isEmptyResources = object =>  isEmpty(object) || Object.keys(object).includes('');

const isNotEmptyArray = arr => Array.isArray(arr) && arr.length > 0;

export {
	isEmpty,
	isEmptyResources,
	isNotEmptyArray
};
