import {transformToTypeahead} from './transforms/transformToTypeahead';
import {transformToTree} from './transforms/transformToTree';
import {createLog} from '../../../utils/logger';
import {isNotEmptyArray, isEmpty} from '../../../utils/objUtils';
import {isMaxLimitOfMetricsHasReached, numOfMetrics} from "./utils/metricUtils";

const LOG = createLog('snEmMetricNavigatorPanelTransform');

const applyCISearchFilter = (ciSysId, data, searchFilter = '', chartMetadata,
	treeSelectionChanged = [], disableNotSelected = false) => {
	LOG.info('applyCISearchFilter start', data, searchFilter);

	const metricsData = {
		ciName: data && data.ci_name,
		additional_metrics: data && data.additional_metrics? data.additional_metrics : [],
		favourite_metrics: data && data.favourite_metrics? data.favourite_metrics : []
	};
	const filter = searchFilter.toLowerCase();

	const newData = {
		additional_metrics: metricsData.additional_metrics.filter(item => item['metric_type'].toLowerCase().includes(filter)),
		favourite_metrics: metricsData.favourite_metrics.filter(item => item['metric_type'].toLowerCase().includes(filter))
	};
	const count = newData ? newData.favourite_metrics.length + newData.additional_metrics.length : 0;
	const {items = [], mapping = new Map()} = newData ?
		transformToTree.buildCITree(ciSysId, metricsData.ciName, newData) : {};

	const selectedItems = transformToTree.generateCISelectedItems(ciSysId, items, chartMetadata, treeSelectionChanged);
	transformToTree.enableItemsSelectionOnCI(items, selectedItems, disableNotSelected);

	const expandedItems = transformToTree.generateCIExpandedItems(ciSysId,selectedItems, !!filter);

	LOG.info('applyCISearchFilter end', count, items);
	return {
		count,
		items,
		mapping,
		expandedItems,
		selectedItems
	};
};

const applyHostSearchFilter = (host = {}, searchFilter = '', chartMetadata,
	treeSelectionChanged = [], disableNotSelected = false) => {
	LOG.info('applyHostSearchFilter start', searchFilter);

	const items = (host && host.host_sys_id) ? transformToTree.buildHostTree(host, searchFilter) : [];
	const {metrics, mapping} = transformToTree.extractMetricsUnderHost(items);

	const count = metrics.length;
	const selectedItems = transformToTree.generateHostSelectedItems(items, treeSelectionChanged);
	transformToTree.enableItemsSelectionOnHost(items, selectedItems, disableNotSelected);

	const expandedItems = (searchFilter.length > 0) ?
		transformToTree.expandAllUnderHost(items) :
		transformToTree.generateHostExpandedItems(selectedItems, [host.host_sys_id]);

	LOG.info('applyHostSearchFilter end', items);
	return {
		count,
		items,
		mapping,
		selectedItems,
		expandedItems
	};
};

const applyServiceSearchFilter = (data = {}, searchFilter = '', chartMetadata,
	treeSelectionChanged = [], disableNotSelected = false) => {
	LOG.info('applyServiceSearchFilter start', searchFilter);

	const items = (isNotEmptyArray(data)) ? transformToTree.buildServicesTree(data, searchFilter) : [];
	const {metrics, mapping} = transformToTree.extractMetricsUnderServices(items);

	const count = metrics.length;
	const selectedItems = transformToTree.generateServiceSelectedItems(items, treeSelectionChanged);
	transformToTree.enableItemsSelectionOnService(items, selectedItems, disableNotSelected);

	const expandedItems = (searchFilter.length > 0 )?
		transformToTree.expandAllUnderServices(items) : transformToTree.generateServiceExpandedItems(selectedItems);

	LOG.info('applyServiceSearchFilter end', items);
	return {
		count,
		items,
		mapping,
		selectedItems,
		expandedItems
	};

};

const checkIfMaxLimitHasReached = (isMaxLimitReached, chartMetadata, treeSelectionChanged) => {
	if (chartMetadata && chartMetadata.metrics && isEmpty(treeSelectionChanged)) {
		const metricsLength = numOfMetrics(chartMetadata.metrics);
		return isMaxLimitOfMetricsHasReached(metricsLength);
	}

	return isMaxLimitReached;
};

export const transformState = state => {

	LOG.info('transformState');

	const {	search: {searchFilter},
		properties: {metricsCiData, metricsHostData, metricsServiceData, chartMetadata},
		treeSelectionChanged, isMaxLimitReached: maxLimitReached, isShowNotification = null} = state;
	const {ci: {ciSysId, data: ciData} = {}} = metricsCiData;
	const {host: {data: hostData} = {}} = metricsHostData;
	const {service: { data: serviceData } = {}} = metricsServiceData;

	const entities = {
		metrics: new Set([
			...metricsCiData.entities.metrics,
			...metricsHostData.entities.metrics,
			...metricsServiceData.entities.metrics]),
		ci: metricsHostData.entities.ci,
		class: metricsHostData.entities.class
	};

	const isMaxLimitReached = checkIfMaxLimitHasReached(maxLimitReached, chartMetadata, treeSelectionChanged);
	const showNotification = isShowNotification === null ? isMaxLimitReached : isShowNotification;

	const searchItems = transformToTypeahead.getItems(entities);
	const newState = {
		...state,
		search: {
			...state.search,
			searchItems
		},
		treeExplorer: {
			ci: {
				...applyCISearchFilter(ciSysId, ciData, searchFilter, chartMetadata, treeSelectionChanged, isMaxLimitReached),
			},
			host: {
				...applyHostSearchFilter(hostData, searchFilter, chartMetadata, treeSelectionChanged, isMaxLimitReached),
			},
			service: {
				...applyServiceSearchFilter(serviceData, searchFilter, chartMetadata, treeSelectionChanged, isMaxLimitReached)
			}
		},
		isShowNotification: showNotification
	};

	LOG.info('transformState newState', newState);
	return newState;
};
