import './components/sn-em-metric-header';
import './components/sn-em-metric-work-area';
import './components/sn-em-metric-navigator-panel';
import '@servicenow/now-toggle';
import {createLog} from '../utils/logger';
import {getUniqueProviderId, getPropertiesListFromProvider} from '../utils/providerUtils';

const LOG = createLog('snEmMetricView');

const getStartAndEndDates = state => {
	const {actualStartTime, actualEndTime, properties} = state;
	const {startTime, endTime} = properties;

	const startDate = actualStartTime || startTime;
	const endDate = actualEndTime || endTime;

	return {
		startDate,
		endDate
	};
};

const getProviderProperties = (providerId) => {
	const mappedProviderProperties = getPropertiesListFromProvider(providerId, [
		'ciName',
		'metricsCIData',
		'metricsHostData',
		'metricsServiceData',
		'hasError',
		'chartMetadata',
		'dataAvailable',
		'title',
		'empty',
		'timeConfig',
		'datePickerChanged'
	]);

	return mappedProviderProperties;
};

const getProviderJSX = params => {
	const {state, providerProperties} = params;
	const {providerId} = providerProperties;

	const {
		properties: {
			ciSysId,
			chartIndex,
			ciType
		},
		showAnnotationsOnChart,
		chartMetricsOperation,
		metricMetadata,
		refresh,
		datePickerChanged,
		timePreset,
		startTimeGMT,
		endTimeGMT,
	} = state;

	const {startDate, endDate} = getStartAndEndDates(state);

	return (
		<sn-em-metric-explorer-provider
			component-id={providerId}
			ci-sys-id={ciSysId}
			ci-type={ciType}
			chart-index={chartIndex}
			start-time={startDate}
			end-time={endDate}
			show-annotations-on-chart={showAnnotationsOnChart}
			metrics-operation={chartMetricsOperation}
			metric-metadata={metricMetadata}
			refresh-data={refresh}
			date-picker-changed={datePickerChanged}
			time-preset={timePreset}
			start-time-gmt={startTimeGMT}
			end-time-gmt={endTimeGMT}
		/>
	);
};

const getWorkAreaJSX = params => {
	const {state, providerProperties} = params;
	const {collapsed, properties, showAnnotationsOnChart} = state;
	const {ciSysId} = properties;

	const {startDate, endDate} = getStartAndEndDates(state);

	const {
		hasError,
		chartIndex,
		chartMetadata,
		title,
		dataAvailable,
		providerId,
		empty,
		timeConfig,
		datePickerChanged
	} = providerProperties;

	return (
		<sn-em-metric-work-area
			collapsed={collapsed}
			ci-sys-id={ciSysId}
			start-time={startDate}
			end-time={endDate}
			empty={empty}
			has-error={hasError}
			chart-metadata={chartMetadata}
			data-available={dataAvailable}
			chart-title={title}
			chart-index={chartIndex}
			provider-id={providerId}
			time-config={timeConfig}
			date-picker-changed={datePickerChanged}
			annotations-toggled={showAnnotationsOnChart}
		/>
	);
};

const getNavigatorPanelJSX = providerProperties => {
	const {
		collapsed,
		chartMetadata,
		metricsCIData,
		metricsHostData,
		metricsServiceData,
		chartIndex
	} = providerProperties;

	return (
		<sn-em-metric-navigator-panel
			collapsed={collapsed}
			chart-metadata={chartMetadata}
			metrics-ci-data={metricsCIData}
			metrics-host-data={metricsHostData}
			metrics-service-data={metricsServiceData}
			metrics-selected={`${chartMetadata}.metrics`}
			chart-index={chartIndex}
		/>
	);
};

const getHeaderJSX = (providerProperties, lastRefreshedTimeAgo) => {
	const {ciName} = providerProperties;

	return (
		<sn-em-metric-header ci-name={ciName} last-refreshed-time-ago={lastRefreshedTimeAgo}/>
	);
};

const getMainJSX = params => {
	const {state, providerProperties} = params;
	const {collapsed, properties: {chartIndex}} = state;
	const {providerId} = providerProperties;

	const collapsedExpandClassInfo = {
		className: collapsed ? '' : 'collapsed'
	};

	return (
		<main>
			<aside {...collapsedExpandClassInfo}>
				{getNavigatorPanelJSX({collapsed, chartIndex,...providerProperties})}
			</aside>
			{getWorkAreaJSX({state, providerProperties: {providerId, ...providerProperties, chartIndex}})}
		</main>
	);
};

export const view = state => {
	const {properties: {ciSysId, chartIndex}, refresh} = state;
	const {lastRefreshedTimeAgo} = refresh;

	LOG.info('Rendering ciSysId', ciSysId);

	const providerId = getUniqueProviderId('', 'sn-em-metric-chart-provider', ciSysId, chartIndex);
	const providerProperties = getProviderProperties(providerId);

	return (
		<div>
			{ciSysId && getProviderJSX({
				state,
				providerProperties: {
					providerId,
					...providerProperties
				}
			})
			}

			{getHeaderJSX(providerProperties, lastRefreshedTimeAgo)}
			{getMainJSX({state, providerProperties: {providerId, ...providerProperties}})}
		</div>
	);
};
