import {createHttpEffect} from '@servicenow/ui-effect-http';
import {ENDPOINTS} from '../consts';
import {createLog} from '../../../../utils/logger';
import {metricActions} from '../../../actions/metricActions';

const LOG = createLog('timeZoneActions');

const ACTION_TYPES = {
	TIMEZONE_FETCH_STARTED: 'TIMEZONE_FETCH#STARTED',
	TIMEZONE_FETCH_SUCCEDED: 'TIMEZONE_FETCH#SUCCEDED',
	TIMEZONE_FETCH_FAILED: 'TIMEZONE_FETCH#FAILED'
};

const getTimeZone = createHttpEffect(ENDPOINTS.TIMEZONE_ENDPOINT, {
	method: 'GET',
	successActionType: ACTION_TYPES.TIMEZONE_FETCH_SUCCEDED,
	errorActionType: ACTION_TYPES.TIMEZONE_FETCH_FAILED,
	batch: false
});

const getTimeZoneSucceeded = coeffects => {
	LOG.info('getTimeZoneSucceeded');
	const {action: {payload}, updateProperties, dispatch} = coeffects;
	const {result} = payload;

	updateProperties({
		timeConfig: {
			...result
		}
	});

	dispatch([metricActions.ACTION_TYPES.UPDATE_TIMEZONE_REQUESTED], {
		timeConfig: {...result}
	});
};

const timeZoneFecthFailed = () => {
	LOG.info('timeZoneFecthFailed');
};

const actionHandlers = {
	[ACTION_TYPES.TIMEZONE_FETCH_STARTED]: getTimeZone,
	[ACTION_TYPES.TIMEZONE_FETCH_SUCCEDED]: getTimeZoneSucceeded,
	[ACTION_TYPES.TIMEZONE_FETCH_FAILED]: timeZoneFecthFailed
};

export const timeZoneActions = {
	ACTION_TYPES,
	actionHandlers
};
