import moment from 'moment';
import momentTz from 'moment-timezone';
import {createLog} from '../../utils/logger';
import {getOffsetBasedOnTimeAndZoneInMs, getStartAndEndTimesForPreset} from '../../utils/dateUtils';

const LOG = createLog('refreshActions');

const ACTION_TYPES = {
	REFRESH_DATA_REQUESTED: 'REFRESH_DATA#REQUESTED',
	REFRESH_DATA_COMPLETED: 'REFRESH_DATA#COMPLETED',
	REFRESH_PROPERTY_REQUESTED: 'REFRESH_PROPERTY_REQUESTED',
	REFRESH_LAST_TIME_UPDATED: 'REFRESH_LAST_TIME#UPDATED'
};

const CUSTOM_RANGE = 'custom';

const _calculateTimesForPreset = params => {
	const {timePreset, timeConfig: {zone}} = params;

	//calculate offset
	const offsetHours = momentTz().tz(zone).utcOffset()/60;

	const localOffsetHours = new Date().getTimezoneOffset()/60;

	const presetHours = parseInt(timePreset) * -1; //we need negative number for the "last [number] hours"
	const offsetWithTimezone = presetHours + offsetHours + localOffsetHours;
	const startTime = new Date();
	const endTime = new Date();

	startTime.setHours(startTime.getHours() + offsetWithTimezone);
	endTime.setHours(endTime.getHours() + offsetHours + localOffsetHours);

	const {start: queryStartTime, end: queryEndTime} = getStartAndEndTimesForPreset(timePreset);

	return {
		startTime: startTime.getTime(),
		endTime: endTime.getTime(),
		queryStartTime,
		queryEndTime
	};
};

const refreshDataClicked = coeffects => {
	LOG.log('refreshDataClicked fired');
	const {state, updateState, properties} = coeffects;
	const {refresh, actualStartTime, actualEndTime, timePreset = 'custom', timeConfig, datePickerChanged} = state;
	const {zone} = timeConfig;

	let newActualStartTime, newActualEndTime, newStartTimeGmt, newEndTimeGmt;

	if (timePreset.toLowerCase() !== CUSTOM_RANGE) {
		const {startTime, endTime, queryStartTime, queryEndTime} = _calculateTimesForPreset({timePreset, timeConfig});
		newActualStartTime = startTime;
		newActualEndTime = endTime;
		newStartTimeGmt = queryStartTime;
		newEndTimeGmt = queryEndTime;
	} else {
		newActualStartTime = actualStartTime || properties.startTime;
		newActualEndTime = actualEndTime || properties.endTime;

		// datePickerChanged is used to determine if we are first time rendering the page
		// if first time render (datePickerChanged === false), do no perform any convertion on the dates
		// if the picker was changed, i.e user selected diffrent time, we need to adjust the date
		// by it's browser offset
		if (datePickerChanged) {
			const localStartTimeOffset = (new Date(actualStartTime).getTimezoneOffset() * 60 * 1000) * -1;
			const localEndTimeOffset = (new Date(actualEndTime).getTimezoneOffset() * 60 * 1000) * -1;
			const startTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, actualStartTime);
			const endTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, actualEndTime);
			newStartTimeGmt = actualStartTime + (-1 * startTimeOffset) + localStartTimeOffset;
			newEndTimeGmt = actualEndTime + (-1 * endTimeOffset) + localEndTimeOffset;
		} else {
			newStartTimeGmt = newActualStartTime;
			newEndTimeGmt = newActualEndTime;
		}
	}

	updateState({
		refresh: {
			...refresh,
			refreshData: true,
			lastTimeRefreshed: new Date().getTime()
		},
		actualStartTime: newActualStartTime,
		actualEndTime: newActualEndTime,
		startTimeGMT: newStartTimeGmt,
		endTimeGMT: newEndTimeGmt
	});
};

const _setLastRefreshedTimeAgo = coeffects => {
	LOG.log('_setLastRefreshedTimeAgo fired');
	const {state: {refresh}, updateState} = coeffects;
	const {lastTimeRefreshed} = refresh;
	const lastRefreshedTimeAgo = moment(lastTimeRefreshed).fromNow();

	updateState({
		refresh: {
			...refresh,
			lastRefreshedTimeAgo
		}
	});
};

const refreshDataCompleted = coeffects => {
	LOG.log('refreshDataCompleted fired');

	const {dispatch} = coeffects;
	dispatch(ACTION_TYPES.REFRESH_PROPERTY_REQUESTED, {value: false});
};

const changeRefreshDataProperty = coeffects => {
	LOG.log('changeRefreshDataProperty fired');
	const {state, updateState} = coeffects;
	const {refresh} = state;
	const lastTimeRefreshed = new Date().getTime();

	updateState({
		refresh: {
			...refresh,
			refreshData: false,
			lastTimeRefreshed,
			lastRefreshedTimeAgo: moment(lastTimeRefreshed).fromNow()
		}
	});
};

const actionHandlers = {
	[ACTION_TYPES.REFRESH_DATA_REQUESTED]: refreshDataClicked,
	[ACTION_TYPES.REFRESH_DATA_COMPLETED]: refreshDataCompleted,
	[ACTION_TYPES.REFRESH_PROPERTY_REQUESTED]: changeRefreshDataProperty,
	[ACTION_TYPES.REFRESH_LAST_TIME_UPDATED]: _setLastRefreshedTimeAgo
};

export const refreshActions = {
	ACTION_TYPES,
	actionHandlers
};
