import {actionTypes} from '@servicenow/ui-core';
import {createLog} from '../../../../utils/logger';
import {dashboardActions} from './dashboardActions';
import {chartActions} from './chartActions';
import {timeZoneActions} from './timezoneActions';
import {metricDataActions} from './metricDataActions';
import {alertAnnotationsActions} from './alertAnnotationsActions';
import {PROPERTY_NAME, CHART_SERIES, METRIC_CHART_OPERATION} from '../consts';
import {ciActions} from './ciActions';
import {createPayloadForChartFetch, sortMetricsByRelatedCI} from '../helpers/helpers';
import {metricActions} from '../../../actions/metricActions';
import {debounce} from 'lodash';

const LOG = createLog('metricChartActions');
const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED, COMPONENT_DISCONNECTED} = actionTypes;

const ACTION_TYPES = {
	METRICS_DATA_INITIAL_FETCH_REQUESTED: 'METRICS_DATA_INITIAL_FETCH#REQUESTED',
	...timeZoneActions.ACTION_TYPES,
	...dashboardActions.ACTION_TYPES,
	...chartActions.ACTION_TYPES,
	...alertAnnotationsActions.ACTION_TYPES,
	...metricDataActions.ACTION_TYPES,
	...ciActions.ACTION_TYPES,
	...metricActions.ACTION_TYPES
};

const init = coeffects => {
	LOG.info('init ' + JSON.stringify(coeffects));

	const {updateProperties} = coeffects;

	const {
		dispatch,
		properties: {ciType, ciSysId, chartIndex}
	} = coeffects;

	dispatch(ACTION_TYPES.TIMEZONE_FETCH_STARTED, {});
	dispatch(ACTION_TYPES.CI_NAME_REQUESTED, {ciSysId});

	//chartIndex will be undefined if it opened from Metric Explorer button
	if (chartIndex >= 0) {
		dispatch(ACTION_TYPES.DASHBOARD_FETCH_STARTED, {
			sys_id: ciSysId,
			sys_class_name: ciType
		});
	} else {
		updateProperties({
			empty: true
		});
	}

	dispatch(ACTION_TYPES.METRIC_CI_FETCH_STARTED, {
		ciSysId
	});
	dispatch(ACTION_TYPES.METRIC_HOST_FETCH_STARTED, {
		ciSysId
	});
	dispatch(ACTION_TYPES.METRIC_SERVICE_FETCH_STARTED, {
		ciSysId
	});

	dispatch(ACTION_TYPES.QUEUE_ACTIONS_PROCESS_STARTED);
};

const onFetchFailed = coeffects => {
	const {action: payload} = coeffects;
	LOG.error(`${payload} failed`);
};

const _handleMetricsOperations = coeffects => {
	LOG.info('_handleMetricsOperations');

	const {properties, dispatch} = coeffects;
	const {metricsOperation, metricMetadata} = properties;

	switch (metricsOperation) {
	case METRIC_CHART_OPERATION.METRIC_ADDED:
		LOG.info('handleMetricsOperations -> METRIC_CHART_OPERATION.METRIC_ADDED case');

		dispatch([ACTION_TYPES.ADD_METRIC_TO_CHART_REQUESTED], {
			metricMetadata
		});
		break;
	case METRIC_CHART_OPERATION.METRIC_REMOVED:
		LOG.info('handleMetricsOperations -> METRIC_CHART_OPERATION.METRIC_REMOVED case');

		dispatch([ACTION_TYPES.REMOVE_METRIC_FROM_CHART_REQUESTED], {
			metricMetadata
		});

		break;
	case METRIC_CHART_OPERATION.METRICS_REMOVED:
		LOG.info('handleMetricsOperations -> METRIC_CHART_OPERATION.METRICS_REMOVE_REQUESTED case');

		dispatch([ACTION_TYPES.REMOVE_METRICS_FROM_CHART_REQUESTED], {
			metricMetadata
		});
		break;

	default:
		break;
	}
};

const _handleShowAnnotationsOperation = coeffects => {
	LOG.info('_handleShowAnnotationsOperation');

	const {
		action: {payload},
		properties,
		updateProperties,
		dispatch
	} = coeffects;

	const {value: showAnnotations} = payload;
	const {chartConfig} = properties;

	LOG.log('propertyChanged -> switch start->', name);

	if (showAnnotations) {
		dispatch([ACTION_TYPES.ALERT_ANNOTATION_REQUESTED]);
	} else {
		const annotationsConfig = {};
		const annotationsData = [];
		const newChartConfig = {
			...chartConfig,
			annotationsConfig,
			annotationsData
		};

		updateProperties({
			chartConfig: newChartConfig
		});
	}
};

const _payloadValuesAreDifferent = payload => {
	const {previousValue, value} = payload;

	return (!isNaN(value) && value !== previousValue) ? true : false;
};

const _handleCiOrChartIndexChange = coeffects => {
	LOG.info('_handleCiOrChartIndexChange');
	const {properties, dispatch, action: {payload}} = coeffects;
	const {ciSysId} = properties;
	const payloadHasNewValues = _payloadValuesAreDifferent(payload);

	if (payloadHasNewValues) {
		dispatch([ACTION_TYPES.DASHBOARD_FETCH_STARTED], {
			sys_id: ciSysId,
			sys_class_name: 'cmdb_ci_win_server' //TODO: Change hard coded value to actual value!
		});
	}
};

const _dispatchUpdateActionsForMetricData =
	({startTime, endTime, metrics, dispatch, updateState}) => {
		const sortedMetrics = sortMetricsByRelatedCI(metrics);

		updateState({
			metricsData: []
		});

		sortedMetrics.forEach((value, key) => {
			const payloadForApiCall = createPayloadForChartFetch({
				ciSysId: key,
				startTime,
				endTime,
				metrics: value
			});
			LOG.info(`_dispatchUpdateActionsForMetricData: batch dispatched with ciSysId: ${key}`, metrics);

			const {params: {start, end}} = payloadForApiCall;
			// prevent sending not numbers values to the server as well as invalid dates
			if (isNaN(parseInt(start)) || isNaN(parseInt(end)) || (start > end)) {
				return;
			}

			dispatch([ACTION_TYPES.CHART_FETCH_STARTED], payloadForApiCall);
		});
	};

const _handleTimeAndDateChange = coeffects => {
	LOG.info('_handleTimeAndDateChange');

	const {properties, state, dispatch, updateState} = coeffects;
	const {startTimeGmt, endTimeGmt, startTime, endTime} = properties;
	const {metricsMetadata} = state;

	_dispatchUpdateActionsForMetricData({
		startTime: startTimeGmt || parseInt(startTime),
		endTime: endTimeGmt || parseInt(endTime),
		metrics: metricsMetadata,
		updateState,
		dispatch
	});
};

const _handleMetricCIData = coeffects => {
	LOG.info('inside _handleMetricCIData');

	const {properties: {metricsCIData}, updateProperties} = coeffects;

	updateProperties({
		metricsCIData
	});
};

const _handleMetricHostData = coeffects => {
	LOG.info('inside _handleMetricHostData');

	const {properties: {metricsHostData}, updateProperties} = coeffects;

	updateProperties({
		metricsHostData
	});
};

const _handleMetricServiceData = coeffects => {
	LOG.info('inside _handleMetricServiceData');

	const {properties: {metricsServiceData}, updateProperties} = coeffects;

	updateProperties({
		metricsServiceData
	});
};

const refreshData = coeffects => {
	LOG.log('refreshData method fired');

	const {properties, state, dispatch, updateProperties, updateState} = coeffects;
	const {refreshData} = properties;
	let startTime, endTime;

	if (!refreshData.actualStartTime || !refreshData.actualEndTime) {
		startTime = properties.startTimeGmt;
		endTime =  properties.endTimeGmt;
	} else {
		startTime = refreshData.actualStartTime;
		endTime = refreshData.actualEndTime;
	}

	const {metricsMetadata} = state;

	updateProperties({
		dataAvailable: false
	});

	_dispatchUpdateActionsForMetricData({
		startTime,
		endTime,
		metrics: metricsMetadata,
		updateState,
		dispatch
	});

	dispatch([ACTION_TYPES.REFRESH_DATA_COMPLETED], {value: false});
};

const debouncedFunc = debounce((coeffects) => {
	_handleTimeAndDateChange(coeffects)
}, 100);

const propertyChanged = coeffects => {
	const {
		action: {payload},
	} = coeffects;

	const {name, value} = payload;

	LOG.log('propertyChanged -> switch start->', name);

	switch (name) {
	case PROPERTY_NAME.METRICS_CI_DATA:
		LOG.log('propertyChanged -> switch -> inside metrics ci data case');
		_handleMetricCIData(coeffects);
		break;
	case PROPERTY_NAME.METRICS_HOST_DATA:
		LOG.log('propertyChanged -> switch -> inside metrics host data case');
		_handleMetricHostData(coeffects);
		break;
	case PROPERTY_NAME.METRICS_SERVICE_DATA:
		LOG.log('propertyChanged -> switch -> inside metrics service data case');
		_handleMetricServiceData(coeffects);
		break;
	case PROPERTY_NAME.SHOW_ANNOTATIONS:
		LOG.log('propertyChanged -> switch -> inside show annotations case');
		_handleShowAnnotationsOperation(coeffects);
		break;
	case PROPERTY_NAME.CHART_INDEX:
	case PROPERTY_NAME.CI_SYS_ID:
		LOG.log('propertyChanged -> switch -> chart index or ciSysId were changed');
		_handleCiOrChartIndexChange(coeffects);
		break;
	case PROPERTY_NAME.START_TIME:
	case PROPERTY_NAME.END_TIME:
		LOG.log('propertyChanged -> switch -> get new chart data since time has changed');

		debouncedFunc(coeffects);
		break;
	case PROPERTY_NAME.METRIC_METADATA:
		_handleMetricsOperations(coeffects);
		break;
	case PROPERTY_NAME.REFRESH_DATA:
		if (value.refreshData) {
			refreshData(coeffects);
		}
		break;
	}
};

const startQueueActionsProcess = coeffects => {
	LOG.log('startQueueActionsProcess fired');

	const {updateState} = coeffects;
	const queueInterval = setInterval(() => {
		chartActions.processQueueActions();
	}, 100);

	updateState({
		queueInterval
	});
};

const stopQueueActionsProcess = coeffects => {
	LOG.log('stopQueueActionsProcess fired');

	const {state: {queueInterval}} = coeffects;
	clearInterval(queueInterval);
};

const metricsInitialFetch = coeffects => {
	const {action: {payload}, dispatch, updateState} = coeffects;
	const {startTime, endTime, metrics} = payload;

	_dispatchUpdateActionsForMetricData({startTime, endTime, metrics, dispatch, updateState});
};

const actions = {
	...alertAnnotationsActions.actions,
	[ACTION_TYPES.CHART_FETCH_STARTED]: {
		schema: {
			type: 'object',
			properties: {
				ci: {
					type: 'object'
				},
				start: {
					type: 'number'
				},
				end: {
					type: 'number'
				},
				tzOffset: {
					type: 'number'
				},
				zone: {
					type: 'string'
				},
				metrics: {
					type: 'array',
					items: [
						{
							type: 'string'
						},
						{
							type: 'string',
							enum: [...CHART_SERIES],
						},
						{
							type: 'string'
						}
					]
				}
			}
		}
	},
	...dashboardActions.actions
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: init,
	[COMPONENT_PROPERTY_CHANGED]: propertyChanged,
	[COMPONENT_DISCONNECTED]: stopQueueActionsProcess,
	[ACTION_TYPES.METRICS_DATA_INITIAL_FETCH_REQUESTED]: metricsInitialFetch,
	...dashboardActions.actionHandlers,
	...timeZoneActions.actionHandlers,
	...alertAnnotationsActions.actionHandlers,
	...metricDataActions.actionHandlers,
	...ciActions.actionHandlers,
	[ACTION_TYPES.CHART_FETCH_FAILED]: onFetchFailed,
	[ACTION_TYPES.QUEUE_ACTIONS_PROCESS_STARTED]: startQueueActionsProcess
};

export const metricExplorerProviderActions = {
	ACTION_TYPES,
	actions,
	actionHandlers
};
