import {t} from 'sn-translate';
import {buildMetricsTree, breakItemToParts} from './transformToMetric';
import {TAB} from '../components/sn-em-metric-navigator-tabs/consts';

const I18N = {
	SERVICE: {
		ENTRY_POINTS_METRICS: t('Entry points'),
		ADDITIONAL_METRICS: t('Additional metrics')
	}
};

const REMOVE_NODE_FROM_TREE = {
	REMOVE_SERVICE: Symbol('REMOVE_SERVICE'),
	REMOVE_ENTRY_POINT: Symbol('REMOVE_ENTRY')
};

const _buildServiceEntryPoint = (entry, filter) => {
	const metrics = buildMetricsTree(entry.metrics, entry.entry_point_sys_id, entry.entry_point_name).metrics;

	let filteredMetrics;
	if (filter.length){
		filteredMetrics = metrics.filter(item => item['metricTypeName'].toLowerCase().includes(filter));
		if (!filteredMetrics.length) {
			return REMOVE_NODE_FROM_TREE.REMOVE_ENTRY_POINT;
		}
	} else {
		filteredMetrics = metrics;
	}

	return {
		id: entry.entry_point_sys_id,
		label: entry.entry_point_name,
		identifierProps: {type: 'icon', icon: 'configuration-item-outline'},
		children: filteredMetrics
	};
};

const _buildServiceEntryPointsTree = (entries , filter) => {
	return entries.map(entry => _buildServiceEntryPoint(entry, filter));
};

const _buildServiceAdditionalTree = (serviceId, serviceName, additional, filter) => {
	const metrics = buildMetricsTree(additional, serviceId, serviceName).metrics;

	let filteredMetrics;
	if (filter.length){
		filteredMetrics = metrics.filter(item => item['metricTypeName'].toLowerCase().includes(filter));
	} else {
		filteredMetrics = metrics;
	}
	return filteredMetrics;
};

const _buildServiceTree = (service, filter) => {
	const entryPoints = _buildServiceEntryPointsTree(service.entries, filter);
	const additional = _buildServiceAdditionalTree(service.service_sys_id, service.service_name, service.additional, filter);

	let filteredEntryPoints;

	if (filter.length) {
		filteredEntryPoints = entryPoints.filter(child => child !== REMOVE_NODE_FROM_TREE.REMOVE_ENTRY_POINT);
		if (!filteredEntryPoints.length) {
			return REMOVE_NODE_FROM_TREE.REMOVE_SERVICE;
		}
	} else {
		filteredEntryPoints = entryPoints;
	}

	return {
		id: service.service_sys_id,
		label: service.service_name,
		identifierProps: {type: 'icon', icon: 'tree-view-long-outline'},
		children: [{
			id: `${service.service_sys_id}-entries`,
			label: I18N.SERVICE.ENTRY_POINTS_METRICS,
			children: filteredEntryPoints
		}, {
			id: `${service.service_sys_id}-additional`,
			label: I18N.SERVICE.ADDITIONAL_METRICS,
			children: additional
		}
		]
	};
};

const buildServicesTree = (data, searchFilter) => {
	const filter = searchFilter.toLowerCase();
	const services = data.map(service => _buildServiceTree(service, filter));

	let filteredServices;

	if (filter.length) {
		filteredServices = services.filter(child => child !== REMOVE_NODE_FROM_TREE.REMOVE_SERVICE);
	} else {
		filteredServices = services;
	}

	return filteredServices;
};

const generateServiceSelectedItems = (tree, treeSelectionChanged) => {
	const selectedItems = (treeSelectionChanged && treeSelectionChanged[TAB.SERVICE])?
		treeSelectionChanged[TAB.SERVICE].selectedItems : [];
	return selectedItems;
};

const generateServiceExpandedItems = (selectedItems = []) => {
	const items = new Set();
	selectedItems.forEach( item => breakItemToParts([], items, [...item]) );

	return Array.from(items);
};

export {
	buildServicesTree,
	generateServiceSelectedItems,
	generateServiceExpandedItems
};
