import '@servicenow/now-template-message';
import {t} from 'sn-translate';
import {createLog} from '../../../utils/logger';
import './components/sn-em-metric-chart';
import {getHeaderJsx} from './views/headerView';
const LOG = createLog('snEmWorkAreaView');
const I18N = {
	NO_DATA: {
		HEADER: t('Your metrics appear as you select them from the left panel.'),
		CONTENT: t('Add items to the chart')
	},
	TOGGLE_PANE: {
		EXPAND: t('Expand chart'),
		COLLAPSE: t('Collapse chart')
	},
	LOADER: {
		LOADING_METRICS: t('Loading metrics...')
	}
};
const getMetricChartJSX = state => {
	const {properties: {ciSysId, startTime, endTime, title, providerId, timeConfig, datePickerChanged}} = state;

	return (
		<sn-em-metric-chart
			ci-sys-id={ciSysId}
			start-time={startTime}
			end-time={endTime}
			title={title}
			provider-id={providerId}
			series-data={`@${providerId}/chartConfig.seriesData`}
			confidence-band-config={`@${providerId}/chartConfig.confidenceBandConfig`}
			time-config={timeConfig}
			date-picker-changed={datePickerChanged}
		/>
	);
};
const getEmptyStateJSX = () => {
	return (
		<now-template-message-empty-state
			content={I18N.NO_DATA.HEADER}
			heading={{'label': I18N.NO_DATA.CONTENT, 'level': '3'}}
			illustration="add-data"
		/>
	);
};
const getPrimaryJsx = state => {
	LOG.info('getMetricChartJsx');
	const {properties: {empty, hasError, dataAvailable, chartMetadata}} = state;
	const className = empty ? 'empty' : '';
	const errorMessage = t('Error displaying chart');
	LOG.info('getMetricChartJsx: '+JSON.stringify(state));
	if (hasError) {
		// TODO: replace with "actual" error mesage or image
		return (
			<div>{errorMessage}</div>
		);
	} else if ((dataAvailable && chartMetadata) || empty) {
		//display EmptyState (data available but empty or for new- chartIndex will be undefined) or the metric chart
		return (
			<main className={className}>
				{empty && !dataAvailable ? getEmptyStateJSX() : getMetricChartJSX(state)}
			</main>
		);
	} else {
		//chart is not ready- display loader
		return (
			<now-loader
				label={I18N.LOADER.LOADING_METRICS}
				size="lg"
				action=""
			/>
		);
	}
};
export const view = (state, {updateState}) => {
	LOG.info('Rendering');
	return (
		<section>
			<div className='work-area-header'>
				{getHeaderJsx(state, updateState)}
			</div>
			<div className='work-area-chart'>
				{getPrimaryJsx(state)}
			</div>
		</section>
	);
};
