import {actionTypes} from '@servicenow/ui-core';
import {createLog} from '../../../../utils/logger';
import {metricActions} from '../../../actions/metricActions';
import { numOfMetrics, isMaxLimitOfMetricsHasReached} from '../utils/metricUtils';

const LOG = createLog('navigationPanelActions');

const {COMPONENT_PROPERTY_CHANGED} = actionTypes;
const ACTION_TYPES = {};

const hideNotification = coeffects => {
	const {updateState} = coeffects;
	updateState({isShowNotification: false});
};

const setSearchFilter = {
	modifier: {name: 'debounce', delay: 1000},
	effect: (coeffects) => {
		const {action: {payload: {value}}, updateState} = coeffects;
		LOG.info('setSearchFilter', value);
		updateState({search: {searchFilter: value}});
	}
};

const _isShowLimitReachedNotification = number => {
	const isMaxLimitReached = isMaxLimitOfMetricsHasReached(number);

	return {
		isMaxLimitReached,
		isShowNotification: isMaxLimitReached
	};
};

const _prepareTreeSelectionPayload = (treeSelectionChanged, name, selectedItems) => ({
	treeSelectionChanged: {
		...treeSelectionChanged,
		name,
		[name]: {
			selectedItems
		}
	}
});

const _removeIrrelevantMetricsFromSelection = (treeSelectionChanged, name, metrics = []) => {
	const selectedItems = [];

	const removedItems = metrics.map( metric => metric.item);
	if (treeSelectionChanged[name] && Array.isArray(treeSelectionChanged[name].selectedItems)) {
		treeSelectionChanged[name].selectedItems.forEach( item => {
			if (!removedItems.includes(item)) selectedItems.push(item);
		});
	}

	return selectedItems;
};

const removeSelectionFromTab = coeffects => {
	const {action: {payload}, updateState, state: {treeSelectionChanged}} = coeffects;
	const {properties: {metricsSelected}} = coeffects;
	const {name, metrics} = payload;

	const numberOfSelectedMetrics = numOfMetrics(metricsSelected) - numOfMetrics(metrics);
	const newSelectedItems = _removeIrrelevantMetricsFromSelection(treeSelectionChanged, name, metrics);
	updateState({
		..._prepareTreeSelectionPayload(treeSelectionChanged, name, newSelectedItems),
		..._isShowLimitReachedNotification(numberOfSelectedMetrics)
	});
};

const treeSelectionChanged = coeffcts => {
	const {action: {payload}, updateState, state: {treeSelectionChanged}} = coeffcts;
	const {name, selectedItems} = payload;

	updateState({
		..._prepareTreeSelectionPayload(treeSelectionChanged, name, selectedItems),
	});
};

const _changeSelectionModifier = (coeffects, modifier) => {
	const {updateState} = coeffects;
	const {properties: {metricsSelected}} = coeffects;

	const numberOfSelectedMetrics = numOfMetrics(metricsSelected) + modifier;
	updateState({
		..._isShowLimitReachedNotification(numberOfSelectedMetrics)
	});
};

const positiveSelectionModifier = coeffects => {
	_changeSelectionModifier(coeffects, 1);
};

const negativeSelectionModifier = coeffects => {
	_changeSelectionModifier(coeffects, -1);
};

const propertyHasChanged = coeffects => {
	const {action: {payload}, updateState} = coeffects;
	const {name, previousValue, value} = payload;

	switch(name){
	case 'chartIndex':
		LOG.info(`differentChartSelected': was ${previousValue} now ${value}`);
		updateState({
			treeSelectionChanged: {},
			isMaxLimitReached: false,
			isShowNotification: false
		});
		break;
	}
};

const actionHandlers = {
	[COMPONENT_PROPERTY_CHANGED]: propertyHasChanged,
	[metricActions.ACTION_TYPES.METRICS_REMOVE_REQUESTED]: removeSelectionFromTab,
	[metricActions.ACTION_TYPES.TREE_SELECTION_CHANGED]: treeSelectionChanged,
	[metricActions.ACTION_TYPES.METRIC_ADD_REQUESTED]: positiveSelectionModifier,
	[metricActions.ACTION_TYPES.METRIC_REMOVE_REQUESTED]: negativeSelectionModifier,
	'NOW_ALERT#ACTION_CLICKED': hideNotification,
	'NOW_TYPEAHEAD#VALUE_SET': setSearchFilter
};

export const navigationPanelActions = {
	ACTION_TYPES,
	actionHandlers
};
