import '@servicenow/now-button';
import '@servicenow/now-date-range-picker';
import '@servicenow/now-toggle';
import '@servicenow/now-select';
import '@servicenow/now-tooltip';
import {t} from 'sn-translate';
import {createLog} from '../../../../utils/logger';
import {getOffsetBasedOnTimeAndZoneInMin, getOffsetBasedOnTimeAndZoneInMs} from '../../../../utils/dateUtils';

const LOG = createLog('snEmWorkAreaView');
const I18N = {
	TOGGLE_PANE: {
		EXPAND: t('Expand chart'),
		COLLAPSE: t('Collapse chart')
	},
	SHOW_ANNOTATIONS: t('Show alerts on CI'),
	SELECT_DATE_RANGE: {
		items: [
			{'id': '1', 'label': t('Last 1 hour')},
			{'id': '3', 'label': t('Last 3 hours')},
			{'id': '5', 'label': t('Last 5 hours')},
			{'id': '24', 'label': t('Last 24 hours')},
			{'id': 'CUSTOM', 'label': t('Custom range')}
		]
	}
};
const PRESETS = I18N.SELECT_DATE_RANGE.items.map(item => {
	return {
		id: item.id,
		label: item.label,
		start: {
			offset: '-' + item.id,
			unit: 'hours',
			inclusive: true
		}
	};
});
const getExpandCollapsedIconJsx = state => {
	const {properties: {collapsed}} = state;
	const icon = collapsed ? 'list-hide-outline' : 'list-show-outline';
	const tooltip = collapsed ? I18N.TOGGLE_PANE.EXPAND : I18N.TOGGLE_PANE.COLLAPSE;
	LOG.info(`getExpandCollapsedIconJsx->collapsed[${collapsed}]`);
	return (
		<now-button-iconic
			icon={icon}
			variant="tertiary"
			size="md"
			configAria={{'button': {'aria-label': tooltip}}}
			tooltipContent={tooltip}
			bare
		/>
	);
};

const getPresetsWithTimezoneOffset = (offsetHours, localOffsetHours) => {
	return PRESETS.map(preset => {
		const {id, start} = preset;

		if (isNaN(id)) {
			return preset;
		}

		const hoursOffset = parseInt(id) * -1; //we need negative number for the "last [number] hours
		const offsetWithTimezone = hoursOffset + offsetHours + localOffsetHours;

		const newStart = {...start};
		newStart.offset = offsetWithTimezone;

		const newEnd = {...start};
		newEnd.offset = offsetHours + localOffsetHours;

		return {
			...preset,
			start: {
				...newStart
			},
			end: {
				...newEnd
			}
		};
	});
};

const getDateRangePickerJsx = state => {
	const {preset, properties} = state;
	const {startTime, endTime, timeConfig: {date, time, zone}, datePickerChanged} = properties;
	const dateFormat = `${date.toUpperCase()} ${time}`;
	const options = {};

	const offsetHours = getOffsetBasedOnTimeAndZoneInMin(zone, startTime)/60;
	const localOffsetHours = new Date().getTimezoneOffset()/60;

	if (preset && ~~preset) {
		options.preset = preset;
		options.presets = getPresetsWithTimezoneOffset(offsetHours, localOffsetHours);
		options.readonly = true;
	} else {
		//Custom range
		const startTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, startTime);
		const endTimeOffset = getOffsetBasedOnTimeAndZoneInMs(zone, endTime);
		const startTimeWithTimezone = startTime + startTimeOffset + (new Date(startTime).getTimezoneOffset() * 60 * 1000);
		const endTimeWithTimezone = endTime + endTimeOffset + (new Date(endTime).getTimezoneOffset() * 60 * 1000);

		options.start = datePickerChanged ? new Date(startTime) : new Date(startTimeWithTimezone);
		options.end = datePickerChanged ? new Date(endTime) : new Date(endTimeWithTimezone);
	}

	LOG.info('getDateRangePicker->options-', options, dateFormat);

	const key = `key_${Date.now()}_${preset}`;

	return (
		<now-date-range-picker
			key={key}
			has-time
			label-start=" "
			label-end=" "
			disabledDates={[{after: new Date()}]}
			format={dateFormat}
			{...options}
		/>
	);
};
const setTargetRef = (updateState, add) => ({elm}) => {
	if (elm) {
		updateState({headerRef: add ? elm : null});
	}
};

export const getHeaderJsx = (state, updateState) => {
	const {headerRef, preset, properties: {chartTitle, annotationsToggled}, empty} = state;

	const selectedItems = preset ? preset : 'CUSTOM';

	return (
		<div className='work-area-toolbar'>
			<div className='work-area-toolbar-expand-icon'>
				{getExpandCollapsedIconJsx(state)}
				<now-heading
					label={chartTitle}
					wont-wrap={true}
					variant='header-secondary'
					className='heading-overflow'
					hook-insert={setTargetRef(updateState, true)}
					hook-destroy={setTargetRef(updateState, false)}
				/>
				<now-tooltip
					id='headerTooltip'
					delay={300}
					target-ref={headerRef}
					content={chartTitle}
				/>
			</div>
			<div className='work-area-toolbar-actions'>
				<div className='work-area-toolbar-actions-annotation-label'>{I18N.SHOW_ANNOTATIONS}</div>
				<div>
					<now-toggle
						manageChecked
						size='sm'
						append-to-meta={{actionType: 'SHOW_ANNOTATIONS'}}
						checked={annotationsToggled}
						disabled={empty}
					/>
				</div>
				<div className='work-area-toolbar-actions-divider'/>
				<div>
					<now-select
						className='work-area-toolbar-actions-presets'
						search="none"
						items={I18N.SELECT_DATE_RANGE.items}
						selectedItem={selectedItems}/>
				</div>
				<div>
					{getDateRangePickerJsx(state)}
				</div>
			</div>

		</div>
	);
};
