import {createCustomElement} from '@servicenow/ui-core';
import {metricExplorerProviderActions} from './actions/metricExplorerProviderActions';

const defaultTabTypeConfig = {
	entities: { metrics: [], ci: [], class: []},
	ci: {data: {}},
	host: {data: {}},
	service: {data: {}}
};

createCustomElement('sn-em-metric-explorer-provider', {
	properties: {
		componentId: {
			schema: {
				type: 'string'
			}
		},
		ciSysId: {},
		ciType: {},
		ciName: {
			selectable: true
		},
		chartIndex: {},
		startTime: {},
		endTime: {},
		chartConfig: {
			default: {},
			selectable: true
		},
		metricsCIData: {
			default: {...defaultTabTypeConfig},
			selectable: true
		},
		metricsHostData: {
			default: {...defaultTabTypeConfig},
			selectable: true
		},
		metricsServiceData: {
			default: {...defaultTabTypeConfig},
			selectable: true
		},
		title: {
			selectable: true
		},
		dataAvailable: {
			default: false,
			selectable: true
		},
		showAnnotationsOnChart: {
			default: false
		},
		hasError: {
			default: false,
			selectable: true
		},
		chartMetadata: {
			selectable: true
		},
		metricsOperation: {},
		metricMetadata: {},
		empty: {
			selectable: true,
			default: false
		},
		timeConfig: {
			selectable: true,
			default: {
				'offset': -25200000,
				'time': 'HH:mm:ss',
				'zone': 'America/Los_Angeles',
				'datetime': 'yyyy-MM-dd HH:mm:ss',
				'date': 'yyyy-MM-dd'
			}
		},
		refreshData: {},
		datePickerChanged: {
			selectable: true,
			default: false
		},
		timePreset: {},
		startTimeGmt: {},
		endTimeGmt: {}
	},
	actions: {
		...metricExplorerProviderActions.actions
	},
	actionHandlers: {
		...metricExplorerProviderActions.actionHandlers
	}
});
