import {createCustomElement} from '@servicenow/ui-core';
import {view} from './snEmMetricView';
import styles from './snEmMetric.scss';
import {metricActions} from './actions/metricActions';
import {refreshActions} from './actions/refreshActions';

createCustomElement('sn-em-metric', {
	view,
	styles,
	initialState: {
		collapsed: true, //Work area state
		showAnnotationsOnChart: false,
		chartMetricsOperation: {},
		actualStartTime: undefined,
		actualEndTime: undefined,
		refresh: {
			refreshData: false,
			lastTimeRefreshed: new Date().getTime()
		}
	},
	actionHandlers: {
		...metricActions.actionHandlers,
		...refreshActions.actionHandlers
	},
	properties: {
		ciSysId: {},
		ciType: {},
		ciName: {},
		chartIndex: {},
		startTime: {},
		endTime: {}
	},
});
