import {createHttpEffect} from '@servicenow/ui-effect-http';
import {
	buildMetricsData,
	normalizeMetricData,
	getConfigurationData,
	getUnits,
	convertEpochToLocal,
	createPayloadForChartFetch
} from '../helpers/helpers';
import {getChartConfig} from '../helpers/timeSeriesChartConfig';
import {createLog} from '../../../../utils/logger';
import {ENDPOINTS} from '../consts';
import {LABEL_CUSTOM_CHART} from '../consts';
import {deepFreeze} from '../../../../utils/devUtils';
import {getOffsetBasedOnTimeAndZoneInMs} from '../../../../utils/dateUtils';

const LOG = createLog('chartActions');

const ACTION_TYPES = {
	CHART_FETCH_STARTED: 'GRID_FETCH#STARTED',
	CHART_FETCH_SUCCEEDED: 'CHART_FETCH#SUCCEEDED',
	CHART_FETCH_FAILED: 'CHART_FETCH#FAILED',
	ADD_METRIC_TO_CHART_REQUESTED: 'ADD_METRIC_TO_CHART#REQUESTED',
	REMOVE_METRIC_FROM_CHART_REQUESTED: 'REMOVE_METRIC_FROM_CHART#REQUESTED',
	REMOVE_METRICS_FROM_CHART_REQUESTED: 'REMOVE_METRICS_FROM_CHART#REQUESTED',
	REMOVE_ALL_SELECTED_METRICS: 'REMOVE_ALL_SELECTED#METRICS',
	QUEUE_ACTIONS_PROCESS_STARTED: 'QUEUE_ACTIONS_PROCESS#STARTED',
	QUEUE_ACTIONS_PROCESS_ENDED: 'QUEUE_ACTIONS_PROCESS#ENDED',
	OPERATION_IN_QUEUE_COMPLETED: 'OPERATION_IN_QUEUE#COMPLETED',
	QUEUE_ACTION_REMOVE_METRIC_REQUESTED: 'QUEUE_ACTION_REMOVE_METRIC#REQUESTED'
};

const CURRENT_OPERATION_TYPE_IN_QUEUE = deepFreeze({
	ADD_METRIC: Symbol('add_metric'),
	REMOVE_METRIC: Symbol('remove_metric'),
	REMOVE_ALL_METRICS: Symbol('remove_all_metrics')
});

const queueActions = {
	actions: [],
	isReadyForNextAction: true,
	currentOperationType: null
};

const metricsToAddContainer = {
	metrics: []
};

const addActionToQueue = params => {
	const {actionHandler, type} = params;

	queueActions.actions.push({
		actionHandler,
		type
	});
};

const gridFetchData = createHttpEffect(ENDPOINTS.QUERY_ENDPOINT, {
	method: 'POST',
	dataParam: 'params',
	successActionType: ACTION_TYPES.CHART_FETCH_SUCCEEDED,
	errorActionType: ACTION_TYPES.CHART_FETCH_FAILED,
	batch: false
});

const _generateChartConfigurationObject = params => {
	const {state, metrics, startTime, endTime, offsetMsInUserTimezone, zone, ciName, chartMetadata, datePickerChanged} = params;
	const {
		boundsConfiguration,
		dataConfigurations,
		metricsMetadata
	} = getConfigurationData(state, metrics, chartMetadata);

	const startTimeWithTz = convertEpochToLocal(startTime, offsetMsInUserTimezone).valueOf();
	const endTimeWithTz = convertEpochToLocal(endTime, offsetMsInUserTimezone).valueOf();

	const units = getUnits(metrics);
	const dataset = buildMetricsData({boundsConfiguration, dataConfigurations, units, metrics});

	const chartConfig = {...getChartConfig(dataset, startTimeWithTz, endTimeWithTz, zone, ciName, units, datePickerChanged, chartMetadata, offsetMsInUserTimezone)};

	const {title} = dataConfigurations;

	return {
		boundsConfiguration,
		dataConfigurations,
		chartConfig,
		metricsMetadata,
		title
	};
};

const _getChartMetadata = properties => {
	let {chartMetadata} = properties;

	if (chartMetadata) {
		return chartMetadata;
	} else {
		return {
			metrics: []
		};
	}
};

const _convertResourcesIdToObject = metric => {
	const newMetric = {...metric};

	if (typeof newMetric.resources_id === 'string') {
		newMetric.resources_id = {
			[newMetric.resources_id]: newMetric.resource_name || {'': null}
		};

		delete newMetric.resource_name;
	}

	return newMetric;
};

const gridFetchDataSucceeded = coeffects => {
	LOG.info('gridFetchDataSucceeded');

	const {
		action: {payload},
		state,
		updateState,
		updateProperties,
		properties,
		dispatch
	} = coeffects;

	const {
		startTime,
		endTime,
		ciName,
		timeConfig: {zone},
		datePickerChanged
	} = properties;

	const {metricsData = []} = state;

	const {result} = payload;
	const {metrics, ci: ciSysId} = result;

	const metric = normalizeMetricData(startTime, endTime, metrics, ciSysId);

	const newMetricsDataArr = [...metricsData, ...metric];

	let changeTitle = false;

	const chartMetadata = _getChartMetadata(properties);

	if (queueActions.currentOperationType === CURRENT_OPERATION_TYPE_IN_QUEUE.ADD_METRIC) {
		changeTitle = true;

		const metricMetadata = metricsToAddContainer.metrics.shift();
		const metricToAddMetadata = {
			...metricMetadata
		};

		if (!metricToAddMetadata.resources_id) {
			metricToAddMetadata.resources_id = {'': null};
		}

		const convertedMetricToAdd = _convertResourcesIdToObject(metricToAddMetadata);
		const index = chartMetadata.metrics.findIndex(elm => elm.metric_type_id === metricToAddMetadata.metric_type_id && elm.ci_sys_id === metricToAddMetadata.ci_sys_id);

		if (index > -1) {
			const [key, value] = Object.entries(convertedMetricToAdd.resources_id)[0];
			chartMetadata.metrics[index].resources_id[key] = value;
		} else {
			chartMetadata.metrics.push(convertedMetricToAdd);
		}
	}

	try {
		const {annotationsConfig, annotationsData, properties: {showAnnotationsOnChart}} = state;

		const params = _generateChartConfigurationObject({
			state,
			metrics: newMetricsDataArr,
			startTime,
			endTime,
			offsetMsInUserTimezone: getOffsetBasedOnTimeAndZoneInMs(zone, startTime),
			zone,
			ciName,
			chartMetadata,
			datePickerChanged
		});
		const {metricsMetadata, chartConfig} = params;
		const title = changeTitle ? LABEL_CUSTOM_CHART : params.title;

		updateState({
			metricsMetadata,
			metricsData: newMetricsDataArr
		});

		// update chartConfig with annotations (alert indicators)
		if (showAnnotationsOnChart) {
			chartConfig.annotationsConfig = annotationsConfig;
			chartConfig.annotationsData = annotationsData;
		}

		updateProperties({
			chartConfig,
			chartMetadata,
			title,
			dataAvailable: true,
			hasError: false,
			empty: false
		});

		dispatch([ACTION_TYPES.OPERATION_IN_QUEUE_COMPLETED]);
	} catch (error) {
		LOG.error('cannot display chart', error);
		updateProperties({
			hasError: true
		});
	}
};

const fetchFailed = () => {
	LOG.error('gridFetchData failed');
};

const createMetricsForApiRequest = (metricsMetadata = [], metricToAdd) => {
	const newMetricsMetadata = [
		...metricsMetadata
	];

	const {resources_id} = metricToAdd;
	// find metric with the same metric_type_id
	const metric = metricsMetadata.find(metric => metric.metric_type_id === metricToAdd.metric_type_id);

	const newMetricToAdd = {
		...metricToAdd
	};

	if (metric && resources_id) {
		metric.resources_id[resources_id] = '';
	} else {
		newMetricsMetadata.push(newMetricToAdd);
	}

	return newMetricsMetadata;
};

const addMetricToChart = coeffects => {
	const {action: {payload}, properties, dispatch, state} = coeffects;
	const {startTime, endTime, startTimeGmt, endTimeGmt, chartMetadata, timeConfig} = properties;
	const {metricsMetadata: stateMetricsMetadata} = state;
	const {metricMetadata} = payload;

	const params = {
		dispatch,
		metricMetadata: {...metricMetadata},
		startTime: startTimeGmt || startTime,
		endTime: endTimeGmt || endTime,
		chartMetadata: {...chartMetadata},
		stateMetricsMetadata: {...stateMetricsMetadata},
		timeConfig
	};

	const addMetric = params => {
		const {startTime, endTime, chartMetadata, metricMetadata, dispatch} = params;
		const {ci_sys_id: ciSysId} = metricMetadata;

		const {resources_id} = metricMetadata;
		const metricToAddMetadata = {
			...metricMetadata
		};

		if (resources_id) {
			if (typeof resources_id === 'string') {
				metricToAddMetadata.resources_id = {
					[resources_id]: ''
				};
			}
		} else {
			metricToAddMetadata.resources_id = {'': null};
		}

		const metrics = createMetricsForApiRequest([], metricToAddMetadata);

		const payloadForApiFetch = createPayloadForChartFetch({
			ciSysId,
			startTime,
			endTime,
			chartMetadata,
			metrics
		});

		dispatch([ACTION_TYPES.CHART_FETCH_STARTED], payloadForApiFetch);
	};

	metricsToAddContainer.metrics.push(metricMetadata);

	addActionToQueue({
		actionHandler: () => addMetric(params),
		type: CURRENT_OPERATION_TYPE_IN_QUEUE.ADD_METRIC
	});
};

const removeMetricFromArray = (metricsMetaData, indexToRemove) => {
	const arr = [...metricsMetaData];
	arr.splice(indexToRemove, 1);

	return arr;
};

const filterOutMetric = (arr, resources_id, metric_type_id, ciSysId) => {
	if (resources_id) {
		return [...arr.filter(item => !(item.type === metric_type_id && item.resource === resources_id && item.ci === ciSysId))];
	} else {
		return [...arr.filter(item => !(item.type === metric_type_id && item.ci === ciSysId))];
	}
};

const removeMetricFromChart = coeffects => {
	const {action, dispatch} = coeffects;
	action.stopPropagation();

	const {metricMetadata} = action.payload;

	addActionToQueue({
		actionHandler: () => {
			dispatch([ACTION_TYPES.QUEUE_ACTION_REMOVE_METRIC_REQUESTED], {metricMetadata: {...metricMetadata}});
		}
	});
};

const handleRemoveMetricQueueOperation = coeffects => {
	const {state, updateState, updateProperties, dispatch, action, properties} = coeffects;
	action.stopPropagation();

	const {metricsData, metricsMetadata} = state;
	const {metricMetadata} = action.payload;
	const {chartMetadata, timeConfig, ciName} = properties;
	const {startTime, endTime} = properties;
	const {zone} = timeConfig;
	const {metric_type_id, resources_id, ci_sys_id: ciSysId} = metricMetadata;

	// find metricMedata item by metric_type_id
	const metricToChangeIndex = metricsMetadata.findIndex(metadata => metadata.metric_type_id === metric_type_id && metadata.ci_sys_id === ciSysId);

	let modifiedMetricsMetaData;

	/** if resourcesIdKey is present in resources_id object, than we need to remove it.
		 if it is the only key, than we remove the parent metricMedtadata object, if not, than we
		 remove only this key. resource_id can contain several key with the same metric_type_id
	 **/
	if (typeof metricsMetadata[metricToChangeIndex].resources_id === 'string') {
		modifiedMetricsMetaData = removeMetricFromArray(metricsMetadata, metricToChangeIndex);
	} else {
		const keysLength = Object.keys(metricsMetadata[metricToChangeIndex].resources_id).length;
		if (keysLength === 1) {
			// remove object from list of metricsMetadata
			modifiedMetricsMetaData = removeMetricFromArray(metricsMetadata, metricToChangeIndex);
		} else {
			// remove just the key
			modifiedMetricsMetaData = [...metricsMetadata];
			delete modifiedMetricsMetaData[metricToChangeIndex].resources_id[resources_id];
		}
	}

	// metrics data stored in state
	const metrics = filterOutMetric(metricsData, resources_id, metric_type_id, ciSysId);

	chartMetadata.metrics = [...modifiedMetricsMetaData];

	const configurationObject = _generateChartConfigurationObject({
		state,
		metrics,
		startTime,
		endTime,
		offsetMsInUserTimezone: getOffsetBasedOnTimeAndZoneInMs(zone, startTime),
		zone,
		chartMetadata,
		ciName
	});

	const {
		chartConfig,
		metricsMetadata: metricsMetadataNewState,
	} = configurationObject;

	const title = LABEL_CUSTOM_CHART;
	const {annotationsConfig, annotationsData, properties: {showAnnotationsOnChart}} = state;

	// update chartConfig with annotations (alert indicators)
	if (showAnnotationsOnChart) {
		chartConfig.annotationsConfig = annotationsConfig;
		chartConfig.annotationsData = annotationsData;
	}

	//TODO: need to think of a way to remove two async calls
	updateState({
		metricsMetadata: metricsMetadataNewState,
		metricsData: metrics
	});

	updateProperties({
		chartMetadata,
		chartConfig,
		title,
		dataAvailable: !!metrics.length,
		empty: !metrics.length,
		hasError: false
	});

	dispatch([ACTION_TYPES.OPERATION_IN_QUEUE_COMPLETED]);
};

const removeSelectedTabMetrics = coeffects => {
	const {action: {payload}, dispatch} = coeffects;
	const {metricMetadata: metricsToRemove} = payload;

	addActionToQueue({
		actionHandler: () => {
			dispatch([ACTION_TYPES.REMOVE_ALL_SELECTED_METRICS], {metricsToRemove});
		},
		type: CURRENT_OPERATION_TYPE_IN_QUEUE.REMOVE_ALL_METRICS
	});
};

const removeTabMetricsFromChart = coeffects => {
	const {state, updateState, properties, updateProperties, action: {payload}, dispatch} = coeffects;
	const {chartMetadata, timeConfig, ciName} = properties;
	const {metricsData, metricsMetadata} = state;
	const {startTime, endTime, zone} = timeConfig;

	const newMetricsData = [...metricsData];
	const newMetricsMetadata = [...metricsMetadata];

	const {metricsToRemove} = payload;

	const findElementInMetric = (item, metricToRemove) => {
		if (item.resource) {
			return item.type === metricToRemove.metric_type_id &&
				item.resource === metricToRemove.resources_id &&
				item.ci === metricToRemove.ci_sys_id;
		} else {
			return item.type === metricToRemove.metric_type_id &&
				item.ci === metricToRemove.ci_sys_id;
		}
	};

	const findElementsInMetricsMetadata = (item, metricToRemove) => {
		const hasResourcesId = typeof item.resources_id === 'string';

		if (hasResourcesId) {
			return item.metric_type_id === metricToRemove.metric_type_id &&
				item.ci_sys_id === metricToRemove.ci_sys_id &&
				item.resources_id === metricToRemove.resources_id;
		} else {
			return item.metric_type_id === metricToRemove.metric_type_id &&
				item.ci_sys_id === metricToRemove.ci_sys_id;
		}
	};

	metricsToRemove.forEach(metricToRemove => {
		let index = newMetricsData.findIndex(item => findElementInMetric(item, metricToRemove));

		if (index !== -1) {
			newMetricsData.splice(index, 1);
		}

		index = newMetricsMetadata.findIndex(item => findElementsInMetricsMetadata(item, metricToRemove));

		if (index !== -1) {
			newMetricsMetadata.splice(index, 1);
		}
	});

	// add ciName to metrics if there is no one already
	newMetricsMetadata.forEach(metric => {
		if (!metric.ci_name) {
			metric.ci_name = ciName;
		}
	});

	chartMetadata.metrics = [...newMetricsMetadata];

	const offset = getOffsetBasedOnTimeAndZoneInMs(startTime);
	const {chartConfig} = _generateChartConfigurationObject({
		state,
		metrics: newMetricsData,
		startTime,
		endTime,
		offsetMsInUserTimezone: offset,
		zone,
		chartMetadata
	});

	//TODO: need to think of a way to remove two async calls
	updateState({
		metricsMetadata: newMetricsMetadata,
		metricsData: newMetricsData
	});

	const isStillMetricsLeft = Array.isArray(newMetricsMetadata) && newMetricsMetadata.length > 0;
	updateProperties({
		chartMetadata,
		chartConfig,
		hasError: false,
		dataAvailable: isStillMetricsLeft,
		empty: !isStillMetricsLeft
	});

	dispatch([ACTION_TYPES.OPERATION_IN_QUEUE_COMPLETED]);
};

const processQueueActions = () => {
	if (queueActions.actions.length && queueActions.isReadyForNextAction) {
		queueActions.isReadyForNextAction = false;
		const actionToProcess = queueActions.actions.shift();
		const {type} = actionToProcess;

		queueActions.currentOperationType = type;
		actionToProcess.actionHandler();
	}
};

const handleOperationCompletion = () => {
	queueActions.isReadyForNextAction = true;
	queueActions.currentOperationType = null;
};

const actions = {
	[ACTION_TYPES.CHART_FETCH_STARTED]: {
		schema: {
			type: 'object',
			properties: {
				ci: {type: 'string'},
				start: {type: 'number'},
				end: {type: 'number'},
				tzOffset: {type: 'number'},
				zone: {type: 'string'},
				metrics: {
					type: 'array',
					items: {
						type: 'object',
						resource: 'string',
						series: {
							type: 'array',
							items: {
								type: 'string'
							}
						}
					}
				}

			},
			required: ['ci', 'start', 'end', 'tzOffset', 'zone', 'metrics']
		}
	}
};

const actionHandlers = {
	[ACTION_TYPES.CHART_FETCH_STARTED]: gridFetchData,
	[ACTION_TYPES.CHART_FETCH_SUCCEEDED]: gridFetchDataSucceeded,
	[ACTION_TYPES.CHART_FETCH_FAILED]: fetchFailed,
	[ACTION_TYPES.ADD_METRIC_TO_CHART_REQUESTED]: addMetricToChart,
	[ACTION_TYPES.REMOVE_METRIC_FROM_CHART_REQUESTED]: removeMetricFromChart,
	[ACTION_TYPES.REMOVE_METRICS_FROM_CHART_REQUESTED]: removeSelectedTabMetrics,
	[ACTION_TYPES.REMOVE_ALL_SELECTED_METRICS]: removeTabMetricsFromChart,
	[ACTION_TYPES.OPERATION_IN_QUEUE_COMPLETED]: handleOperationCompletion,
	[ACTION_TYPES.QUEUE_ACTION_REMOVE_METRIC_REQUESTED]: handleRemoveMetricQueueOperation
};

export const chartActions = {
	ACTION_TYPES,
	actionHandlers,
	actions,
	processQueueActions
};
