//TODO: take this number dynamically
export const isMaxLimitOfMetricsHasReached = number => number > 9;

/**
 * Count the number of individual metrics
 * @param metrics
 * @returns {number}
 */
export const numOfMetrics = (metrics = []) => {
	let sum = 0;
	metrics.forEach( metric => {
		if (metric.resources_id) {
			const length = Object.keys(metric.resources_id).length;
			sum += (length < 2)? 1 : length;
		} else {
			sum += 1;
		}
	})
	return sum;
};
